import { ModalPanel } from 'maha-admin'
import PropTypes from 'prop-types'
import Search from './search'
import React from 'react'

class Help extends React.Component {

  static contextTypes = {
    modal: PropTypes.object
  }

  static propTypes = {
    article: PropTypes.string,
    items: PropTypes.array,
    q: PropTypes.string,
    onFetch: PropTypes.func,
    onType: PropTypes.func
  }

  _handleDone = this._handleDone.bind(this)
  _handleFetch = this._handleFetch.bind(this)
  _handleType = this._handleType.bind(this)

  render() {
    const { article } = this.props
    return (
      <ModalPanel { ...this._getPanel() }>
        <div className="maha-help">
          <div className="maha-help-nav">
            <div className="maha-help-nav-item">
              <i className="fa fa-fw fa-lightbulb-o" />
              Knowledge Base
            </div>
            <div className="maha-help-nav-item">
              <i className="fa fa-fw fa-ticket" />
              Submit a Support Ticket
            </div>
          </div>
          <div className="maha-help-main">
            { article ?
              <div className="maha-help-article" dangerouslySetInnerHTML={{ __html: article }} /> :
              <Search { ...this._getSearch() } />
            }
          </div>
        </div>
      </ModalPanel>
    )
  }

  _getPanel() {
    return {
      title: 'Help Center',
      rightItems: [
        { label: 'Done', handler: this._handleDone }
      ]
    }
  }

  _getSearch() {
    return {
      q: this.props.q,
      onClick: this._handleFetch,
      onType: this._handleType
    }
  }

  _handleType(q) {
    this.props.onType(q)
  }

  _handleFetch(id) {
    this.props.onFetch(id)
  }

  _handleDone() {
    this.context.modal.close()
  }

}

export default Help
