export const setTyping = (typing) => ({
  type: 'SET_TYPING',
  typing
})

export const showMore = () => ({
  type: 'SHOW_MORE'
})

export const type = (q) => ({
  type: 'TYPE',
  q
})

export const rate = (score) => ({
  type: 'RATE',
  score
})

export const load = (endpoint) => ({
  type: 'API_REQUEST',
  method: 'GET',
  endpoint,
  request: 'LOAD_REQUEST',
  success: 'LOAD_SUCCESS',
  failure: 'LOAD_FAILURE'
})

export const create = (endpoint, review) => ({
  type: 'API_REQUEST',
  method: 'POST',
  endpoint,
  body: { score: review.score, text: review.text, uid: review.uid },
  meta: { review },
  request: 'CREATE_REQUEST',
  success: 'CREATE_SUCCESS',
  failure: 'CREATE_FAILURE'
})

export const update = (endpoint, review) => ({
  type: 'API_REQUEST',
  method: 'PATCH',
  endpoint,
  body: { score: review.score, text: review.text },
  meta: { review },
  request: 'UPDATE_REQUEST',
  success: 'UPDATE_SUCCESS',
  failure: 'UPDATE_FAILURE'
})

export const destroy = (endpoint, review) => ({
  type: 'API_REQUEST',
  method: 'DELETE',
  endpoint,
  meta: { review },
  request: 'DESTROY_REQUEST',
  success: 'DESTROY_SUCCESS',
  failure: 'DESTROY_FAILURE'
})

export const beginEdit = (q, uid) => ({
  type: 'BEGIN_EDIT',
  q,
  uid
})

export const cancelEdit = () => ({
  type: 'CANCEL_EDIT'
})

export const add = (review) => ({
  type: 'ADD',
  review
})

export const replace = (review) => ({
  type: 'REPLACE',
  review
})

export const remove = (uid) => ({
  type: 'REMOVE',
  uid
})
