import { Modal, Prompt, Drawer, Tasks, Tray } from 'reframe'
import PropTypes from 'prop-types'
import Activate from '../activate'
import Portal from '../portal'
import Signin from '../signin'
import Reset from '../reset'
import React from 'react'
import _ from 'lodash'

class Platform extends React.Component {

  static contextTypes = {
    admin: PropTypes.object,
    router: PropTypes.object
  }

  static propTypes = {
    badges: PropTypes.array,
    children: PropTypes.any,
    roots: PropTypes.array,
    routes: PropTypes.object
  }

  render() {
    const { router, admin } = this.context
    const { badges, roots, routes } = this.props
    const { pathname } = router
    if(pathname.match(/^\/admin\/activate/) !== null) return <Activate location={{ pathname }} />
    if(pathname.match(/^\/admin\/reset/) !== null) return <Reset location={{ pathname }}/>
    if(pathname.match(/^\/admin\/signin/) !== null) return <Signin location={{ pathname }} />
    if(!admin.user || !admin.team) return null
    return (
      <div className="maha-platform" key={ admin.user.id }>
        { roots.map((root, index) => {
          if(root.app !== 'maha' && !this._getAccess(root.app)) return null
          return <root.component key={`root_${index}`} />
        }) }
        <Tray>
          <Prompt>
            <Modal>
              <Prompt>
                <Drawer>
                  <Tasks>
                    <Portal badges={ badges } routes={ routes } />
                  </Tasks>
                </Drawer>
              </Prompt>
            </Modal>
          </Prompt>
        </Tray>
      </div>
    )
  }

  _getAccess(code) {
    return (_.findIndex(this.context.admin.apps, { code }) >= 0)
  }

}

export default Platform
