import React from 'react'
import PropTypes from 'prop-types'
import Image from '../image'

class Team extends React.Component {

  static contextTypes = {
    flash: PropTypes.object
  }

  static propTypes = {
    active: PropTypes.number,
    status: PropTypes.string,
    onSet: PropTypes.func,
    onTeam: PropTypes.func
  }

  team = null

  _handleSubmit = this._handleSubmit.bind(this)

  render() {
    const { status } = this.props
    return (
      <div className="maha-signin-panel">
        <div className="maha-signin-form">
          <div className="maha-avatar">
            <div className="maha-avatar-badge">
              <div className="maha-avatar-wrapper">
                <Image src="/images/maha.png" title="The Maha Platform" transforms={{ fit: 'cover', w: 150, h: 150 }} />
              </div>
            </div>
          </div>
          <h2>The Maha Platform</h2>
          <p>Sign in To Your Team</p>
          <form className="ui form" onSubmit={ this._handleSubmit }>
            <div className="field team-field">
              <div className="ui fluid left icon input">
                <i className="user icon"></i>
                <input className="form-control" autoComplete="off" autoCapitalize="off" autoCorrect="off" spellCheck="false" placeholder="team" type="text" ref={ (node) => this.team = node } />
              </div>
            </div>
            <div className="field button-field">
              <button className={`ui fluid large ${(status === 'submitting') ? 'loading' : ''} button`}>Continue <i className="right chevron icon" /></button>
            </div>
          </form>
        </div>
      </div>
    )
  }

  componentDidMount() {
    this.props.onSet(null, null, 'team')
  }

  _handleSubmit(e) {
    const { onTeam } = this.props
    const subdomain = this.team.value
    onTeam(subdomain)
    e.preventDefault()
    return false
  }

}

export default Team
