import Subscriptions from '../subscriptions'
import { Loader, Message } from 'maha-admin'
import Edit from '../../views/edit'
import PropTypes from 'prop-types'
import Channels from '../channels'
import New from '../../views/new'
import Channel from '../channel'
import Starred from '../starred'
import Header from '../header'
import Info from '../info'
import React from 'react'

class Chat extends React.Component {

  static contextTypes = {
    admin: PropTypes.object,
    confirm: PropTypes.object,
    modal: PropTypes.object,
    network: PropTypes.object,
    router: PropTypes.object
  }

  static propTypes = {
    adding: PropTypes.bool,
    channels: PropTypes.array,
    channel: PropTypes.object,
    editing: PropTypes.bool,
    info: PropTypes.bool,
    selected: PropTypes.number,
    managing: PropTypes.bool,
    starred: PropTypes.bool,
    status: PropTypes.string,
    onChoose: PropTypes.func,
    onToggleAdding: PropTypes.func,
    onToggleEditing: PropTypes.func
  }

  _handleChoose = this._handleChoose.bind(this)
  _handleEdit = this._handleEdit.bind(this)
  _handleInfo = this._handleInfo.bind(this)
  _handleNew = this._handleNew.bind(this)
  _handleShowMessage = this._handleShowMessage.bind(this)
  _handleStarred = this._handleStarred.bind(this)
  _handleSubscriptions = this._handleSubscriptions.bind(this)

  render() {
    const { adding, channel, editing, info, managing, starred, status } = this.props
    if(status === 'loading') return <Loader />
    return (
      <div className={ this._getClass() }>
        <div className="fullchat-left">
          <Channels { ...this._getChannels() } />
          { adding && <New { ...this._getNew() } /> }
        </div>
        { channel &&
          <div className="fullchat-channel">
            <Header { ...this._getHeader() } />
            <div className="fullchat-channel-body">
              <Channel { ...this._getChannel() }  key={`channel_${channel.id}`}/>
              <div className="fullchat-right">
                { info && <Info { ...this._getInfo() } key={`info_${channel.id}`} /> }
                { editing && <Edit { ...this._getEdit() } /> }
                { managing && <Subscriptions { ...this._getSubscriptions() } /> }
              </div>
            </div>
          </div>
        }
        { starred &&
          <div className="fullchat-channel">
            <Header { ...this._getStarredHeader() } />
            <div className="fullchat-channel-body">
              <Starred { ...this._getStarred() } />
            </div>
          </div>
        }
        { !channel && !starred &&
          <div className="fullchat-channel">
            <Message { ...this._getEmpty() } />
          </div>
        }
      </div>
    )
  }

  componentDidMount() {
    const { channels, params, pathname, onChoose, onStarred } = this.props
    if(pathname.match(/^\/admin\/chat$/)) {
      if(channels.length === 0) return
      onChoose(channels[0].id)
    } else if(pathname.match(/^\/admin\/chat\/channels\/\d*/)) {
      onChoose(parseInt(params.id))
    } else if(pathname.match(/^\/admin\/chat\/starred$/)) {
      onStarred()
    }
  }

  componentDidUpdate(prevProps) {
    const { channels, onChoose } = this.props
    if(channels.length > prevProps.channels.length) {
      onChoose(channels[0].id)
    }
  }

  _getClass() {
    const { info, adding, editing, managing } = this.props
    const classes = ['fullchat']
    if(info) classes.push('info')
    if(adding) classes.push('adding')
    if(editing) classes.push('editing')
    if(managing) classes.push('managing')
    return classes.join(' ')
  }

  _getChannels() {
    const { channels, selected, status } = this.props
    return {
      channels,
      selected,
      showNew: true,
      status,
      onChoose: this._handleChoose,
      onNew: this._handleNew,
      onStarred: this._handleStarred
    }
  }

  _getEmpty() {
    return {
      icon: 'comment',
      title: 'No Channel',
      text: 'Choose a channel from the list'
    }
  }

  _getHeader() {
    const { channel } = this.props
    return {
      ...channel,
      onInfo: this._handleInfo
    }
  }

  _getStarredHeader() {
    return {
      name: 'Starred Messages',
      description: 'This is a collection of starred messages'
    }
  }

  _getChannel() {
    const { channel, selected } = this.props
    return {
      channel,
      id: selected,
      onShowMessage: this._handleShowMessage
    }
  }

  _getInfo() {
    const { channel, selected } = this.props
    return {
      channel,
      id: selected,
      showHeader: false,
      onEdit: this._handleEdit,
      onSubscriptions: this._handleSubscriptions
    }
  }

  _getNew() {
    return {
      onCancel: this._handleNew,
      onSuccess: this._handleNew
    }
  }

  _getStarred() {
    return {
      onShowMessage: this._handleShowMessage
    }
  }

  _getEdit() {
    const { selected } = this.props
    return {
      id: selected,
      onCancel: this._handleEdit,
      onSuccess: this._handleEdit
    }
  }

  _getSubscriptions() {
    const { user } = this.context.admin
    const { channel } = this.props
    return {
      channel,
      endpoint: `/api/admin/chat/channels/${channel.id}/subscriptions`,
      subscription_ids: channel.subscriptions.filter(subscription => {
        return subscription.id !== user.id
      }).map(subscription => subscription.id),
      onCancel: this._handleSubscriptions,
      onDone: this._handleSubscriptions
    }
  }

  _handleChoose(id) {
    this.context.router.replace(`/admin/chat/channels/${id}`)
    this.props.onChoose(id)
  }

  _handleEdit() {
    this.props.onToggleEditing()
  }

  _handleInfo() {
    this.props.onToggleInfo()
  }

  _handleNew() {
    this.props.onToggleAdding()
  }

  _handleShowMessage(message) {
    const { router } = this.context
    router.push(`/admin/chat/channels/${message.channel_id}/messages/${message.id}`)
  }

  _handleStarred() {
    this.context.router.replace('/admin/chat/starred')
    this.props.onStarred()
  }

  _handleSubscriptions() {
    this.props.onToggleManaging()
  }

}

export default Chat
