import PropTypes from 'prop-types'
import React from 'react'

const CompactReviewToken = ({ score }) => {

  const adjusted = Math.round(score * 2) / 2

  const full = Math.floor(adjusted)

  const half = (adjusted * 2) % 2

  const empty = 5 - full - half

  return (
    <div className="maha-review-token">
      { [...Array(full)].map((i, index) => (
        <i className="fa fa-star" key={`review_full_${index}`} />
      ))}
      { half > 0 && <i className="fa fa-star-half-o" /> }
      { [...Array(empty)].map((i, index) => (
        <i className="fa fa-star-o" key={`review_empty_${index}`} />
      ))}
    </div>
  )

}

CompactReviewToken.propTypes = {
  score: PropTypes.number
}

export default CompactReviewToken
