import PropTypes from 'prop-types'
import React from 'react'
import _ from 'lodash'

class Browser extends React.Component {

  static childContextTypes = {
    host: PropTypes.object
  }

  static contextTypes = {
    alerts: PropTypes.object
  }

  static propTypes = {
    children: PropTypes.any,
    push: PropTypes.string,
    requesting: PropTypes.bool,
    onLoadPush: PropTypes.func,
    onRequestPermission: PropTypes.func,
    onSavePush: PropTypes.func,
    onSaveSubscription: PropTypes.func,
    onSetPermission: PropTypes.func
  }

  alert = null

  _handleAskNextTime = this._handleAskNextTime.bind(this)
  _handleDismissNotifications = this._handleDismissNotifications.bind(this)
  _handleNeverAskAgain = this._handleNeverAskAgain.bind(this)
  _handleNotification = this._handleNotification.bind(this)
  _handlePromptNotifications = this._handlePromptNotifications.bind(this)
  _handleEnableNotifications = this._handleEnableNotifications.bind(this)

  render() {
    return this.props.children
  }

  componentDidMount() {
    this.props.onLoadPush()
  }

  componentDidUpdate(prevProps) {
    const { push, onSavePush } = this.props
    if(push !== prevProps.push ){
      if( push === 'unknown') {
        this._handleAskNotification()
      } else if( push === 'granted') {
        onSavePush('granted')
      }
    }
  }

  getChildContext() {
    return {
      host: {
        notifications: this._getNotifications(),
        pushNotification: this._handleNotification,
        promptNotifications: this._handlePromptNotifications
      }
    }
  }

  _getAskAlert() {
    return {
      priority: 2,
      color: 'blue',
      component: () => (
        <div className="maha-browser-alert">
          <div className="maha-browser-alert-message">
            <p>We need your permission to <span onClick={ this._handlePromptNotifications }>enable desktop notifications</span></p>
          </div>
          <div className="maha-browser-alert-dismiss" onClick={ this._handleDismissNotifications }>
            <i className="fa fa-times" />
          </div>
        </div>
      )
    }
  }

  _getConfirmAlert() {
    return {
      priority: 2,
      color: 'green',
      component: () => (
        <div className="maha-browser-alert">
          <div className="maha-browser-alert-message">
            <p>
              We recommend enabling desktop notifications on this device &bull;&nbsp;
              <span onClick={ this._handlePromptNotifications }>Enable Notifications</span> &bull;&nbsp;
              <span onClick={ this._handleAskNextTime }>Ask me next time</span> &bull;&nbsp;
              <span onClick={ this._handleNeverAskAgain }>Never ask again on this computer</span>
            </p>
          </div>
        </div>
      )
    }
  }

  _getCapability() {
    return window.Notification && navigator.serviceWorker
  }

  _getNotifications() {
    const prop = this._getHiddenProp()
    if(!prop) return false
    const hidden = document[prop]
    return (this._getCapability() && Notification.permission === 'granted' && hidden)
  }

  _getHiddenProp() {
    const prefixes = ['webkit','moz','ms','o']
    if('hidden' in document) return 'hidden'
    for(var i = 0; i < prefixes.length; i++) {
      if((prefixes[i] + 'Hidden') in document) {
        return prefixes[i] + 'Hidden'
      }
    }
    return null
  }

  _getSubscribeOptions(key) {
    const padding = '='.repeat((4 - key.length % 4) % 4)
    const base64 = (key + padding).replace(/-/g, '+').replace(/_/g, '/')
    const rawData = window.atob(base64)
    const applicationServerKey = new Uint8Array(rawData.length)
    for(let i = 0; i < rawData.length; ++i) {
      applicationServerKey[i] = rawData.charCodeAt(i)
    }
    return {
      userVisibleOnly: true,
      applicationServerKey
    }
  }

  _handleAskNotification() {
    if(this._getCapability() && !_.includes(['denied','granted'], Notification.permission)) {
      this.alert = this._getAskAlert()
      this.context.alerts.push(this.alert)
    }
  }

  _handlePromptNotifications() {
    Notification.requestPermission(status => {
      if(status === 'granted') this._handleEnableNotifications()
    })
  }

  _handleDismissNotifications() {
    this.context.alerts.pop()
    this.alert = this._getConfirmAlert()
    this.context.alerts.push(this.alert)
  }

  _handleAskNextTime() {
    this.context.alerts.pop()
  }

  _handleNeverAskAgain() {
    this.props.onSavePush('never')
    this.context.alerts.pop()
  }

  _handleEnableNotifications() {
    const { onSaveSubscription } = this.props
    this._handleNotification({
      title: 'Test Notification',
      body: 'This is a notification'
    })
    navigator.serviceWorker.register('/admin/js/notifications.js').then(registration => {
      return new Promise(resolve => setTimeout(() => resolve(registration), 100))
    }).then(registration => {
      const options = this._getSubscribeOptions(process.env.VAPID_PUBLIC_KEY)
      return registration.pushManager.subscribe(options)
    }).then(subscription => {
      const data = JSON.parse(JSON.stringify(subscription))
      onSaveSubscription({
        agent: navigator.userAgent,
        endpoint: data.endpoint,
        p256dh: data.keys.p256dh,
        auth: data.keys.auth
      })
      return subscription
    }).then(() => {
      this.context.alerts.pop()
    }).catch(err => {
      console.error('Unable to register service worker.', err)
    })
  }

  _handleNotification({ title, body, url, onClick }) {
    const data = {
      icon: '/images/maha.png',
      sound: '/audio/notification.mp3',
      body
    }
    const notification = new Notification(title, data)
    notification.onclick = (e) => {
      e.preventDefault()
      if(onClick) onClick()
      if(url) window.open(url, '_blank')
    }
  }

}

export default Browser
