import { CompactUserToken, ModalPanel, ToggleList } from 'maha-admin'
import PropTypes from 'prop-types'
import React from 'react'

class Subscriptions extends React.Component {

  static contextTypes = {
    admin: PropTypes.object
  }

  static propTypes = {
    channel: PropTypes.object,
    endpoint: PropTypes.string,
    ids: PropTypes.array,
    method: PropTypes.string,
    status: PropTypes.string,
    subscription_ids: PropTypes.array,
    onCancel: PropTypes.func,
    onCreate: PropTypes.func,
    onDone: PropTypes.func,
    onSet: PropTypes.func
  }

  _handleCancel = this._handleCancel.bind(this)
  _handleChange = this._handleChange.bind(this)
  _handleSave = this._handleSave.bind(this)

  render() {
    return (
      <ModalPanel { ...this._getPanel() }>
        <ToggleList { ...this._getToggleList() } />
      </ModalPanel>
    )
  }

  componentDidUpdate(prevProps) {
    const { channel, status, onDone } = this.props
    if(status !== prevProps.status && status === 'success') {
      onDone(channel)
    }
  }

  _getPanel() {
    return {
      title: 'Manage Members',
      leftItems: [
        { label: 'Cancel', handler: this._handleCancel }
      ],
      rightItems: [
        { label: 'Save', handler: this._handleSave }
      ]
    }
  }

  _getToggleList() {
    const { admin } = this.context
    const { subscription_ids } = this.props
    const exclude_ids = [ admin.user.id ]
    return {
      endpoint: '/api/admin/users',
      defaultFilters: { app_id: { $eq: 6 } },
      defaultValue: subscription_ids,
      exclude_ids,
      format: (props) => <CompactUserToken { ...props } presence={ true} />,
      multiple: true,
      text: 'full_name',
      value: 'id',
      onChange: this._handleChange
    }
  }

  _handleChange(ids) {
    this.props.onSet(ids)
  }

  _handleCancel() {
    this.props.onCancel()
  }

  _handleSave() {
    const { endpoint, ids } = this.props
    this.props.onCreate(endpoint, { ids })
  }

}

export default Subscriptions
