export const set = (ignored) => ({
  type: 'SET',
  ignored
})

export const load = () => ({
  type: 'API_REQUEST',
  method: 'GET',
  endpoint: '/api/admin/account/preferences/notifications',
  request: 'LOAD_REQUEST',
  success: 'LOAD_SUCCESS',
  failure: 'LOAD_FAILURE'
})

export const toggle = (id) => ({
  type: 'TOGGLE',
  id
})
