import NotificationMethodToken from '../notification_method_token'
import Subscriptions from '../subscriptions'
import PropTypes from 'prop-types'
import { Form } from 'maha-admin'
import Push from './push'
import React from 'react'

class Preferences extends React.Component {

  static contextTypes = {
    flash: PropTypes.object,
    modal: PropTypes.object
  }

  _handleCancel = this._handleCancel.bind(this)
  _handleSuccess = this._handleSuccess.bind(this)

  render() {
    return <Form { ...this._getForm() } />
  }

  _getForm() {
    return {
      title: 'Notifications',
      method: 'patch',
      endpoint: '/api/admin/account/preferences',
      action: '/api/admin/account/preferences',
      onCancel: this._handleCancel,
      onSuccess: this._handleSuccess,
      before: <Push />,
      sections: [
        {
          fields: [
            { label: 'Delivery Method', instructions: 'If you are not signed in when a notification arrives, we can send you an email. Please indicate how you would like to be contacted when you miss a notification.', name: 'notification_method_id', type: 'radiogroup', endpoint: '/api/admin/notification_methods', value: 'id', text: 'title', format: NotificationMethodToken },
            { label: 'Subscriptions', instructions: 'Please inform me when the following events occur', name: 'ignored', type: Subscriptions }
          ]
        }
      ]
    }
  }

  _handleCancel() {
    this.context.modal.close()
  }

  _handleSuccess(user) {
    this.context.modal.close()
    this.context.flash.set('success', 'Your notification preferences were successfully updated')
  }

}

export default Preferences
