import { CSSTransition } from 'react-transition-group'
import PropTypes from 'prop-types'
import Complete from './complete'
import Security from './security'
import Password from './password'
import Invalid from './invalid'
import Verify from './verify'
import React from 'react'
import _ from 'lodash'

class Reset extends React.Component {

  static contextTypes = {
    admin: PropTypes.object,
    flash: PropTypes.object,
    router: PropTypes.object
  }

  static propTypes = {
    error: PropTypes.string,
    location: PropTypes.object,
    mode: PropTypes.string,
    status: PropTypes.string,
    onVerify: PropTypes.func
  }

  constructor(props) {
    super(props)
    this.classNames = ''
    this.routes = [
      { mode: 'verify', component: Verify },
      { mode: 'invalid', component: Invalid },
      { mode: 'security', component: Security },
      { mode: 'password', component: Password },
      { mode: 'complete', component: Complete }
    ]
  }

  render() {
    const { mode } = this.props
    return (
      <div className="maha-signin">
        { this.routes.map(route => (
          <CSSTransition in={ mode === route.mode } classNames={ this.classNames } timeout={ 500 } mountOnEnter={ true } unmountOnExit={ true } key={route.mode}>
            <route.component { ...this.props } />
          </CSSTransition>
        ))}
      </div>
    )
  }

  componentDidMount() {
    const { location, onVerify } = this.props
    const [,token] = location.pathname.match(/^\/admin\/reset\/(.*)/)
    onVerify(token)
  }

  componentWillUpdate(nextProps) {
    const { mode } = this.props
    if(nextProps.mode !== mode) {
      const oldIndex = _.findIndex(this.routes, { mode })
      const newIndex = _.findIndex(this.routes, { mode: nextProps.mode })
      if(oldIndex >= 0) {
        this.classNames = (newIndex >= oldIndex) ? 'slide-next' : 'slide-back'
      }
    }
  }

  componentDidUpdate(prevProps) {
    const { flash, router } = this.context
    const { error, mode, status } = this.props
    if(prevProps.status !== status && status === 'failure') {
      flash.set('error', error)
    }
    if(mode !== prevProps.mode) {
      const pathname = `/admin/reset/${mode}`
      router.push(pathname)
    }

  }

}

export default Reset
