export const INITIAL_STATE = {
  status: 'pending',
  push: null
}

export default (state = INITIAL_STATE, action) => {

  switch (action.type) {

  case 'SAVE_SUBSCRIPTION_SUCCESS':
    return {
      ...state,
      status: 'success',
      push: 'granted'
    }

  case 'LOAD_PUSH_SUCCESS':
    return {
      ...state,
      push: action.value || 'unknown'
    }

  default:
    return state
  }

}
