import PropTypes from 'prop-types'
import Alert from './alert'
import React from 'react'

class Alerts extends React.Component {

  static childContextTypes = {
    alerts: PropTypes.object
  }

  static contextTypes = {
    router: PropTypes.object
  }

  static propTypes = {
    alerts: PropTypes.array,
    alert: PropTypes.object,
    children: PropTypes.any,
    onPush: PropTypes.func,
    onPop: PropTypes.func
  }

  _handlePush = this._handlePush.bind(this)
  _handlePop =this._handlePop.bind(this)

  render() {
    const { children, alert } = this.props
    const { pathname } = this.context.router
    const showAlerts = !pathname.match(/\/(activate|signin|reset).*/)
    return (
      <div className="maha-alerts">
        { showAlerts && alert && <Alert { ...alert } /> }
        { children }
      </div>
    )
  }

  getChildContext() {
    return {
      alerts: {
        push: this._handlePush,
        pop: this._handlePop
      }
    }
  }

  _handlePush(alert) {
    this.props.onPush(alert)
  }

  _handlePop() {
    this.props.onPop()
  }

}

export default Alerts
