export const init = (connect, disconnect, message, presence) => ({
  type: 'SOCKETIO_INIT',
  connect,
  disconnect,
  message,
  presence
})

export const connect = () => ({
  type: 'CONNECT'
})

export const disconnect = () => ({
  type: 'DISCONNECT'
})

export const join = (channel) => ({
  type: 'SOCKETIO_JOIN',
  channel,
  request: 'JOIN_REQUEST',
  success: 'JOIN_SUCCESS',
  failure: 'JOIN_FAILURE'
})

export const leave = (channel) => ({
  type: 'SOCKETIO_LEAVE',
  channel,
  request: 'LEAVE_REQUEST',
  success: 'LEAVE_SUCCESS',
  failure: 'LEAVE_FAILURE'
})

export const message = (verb, channel, data) => ({
  type: 'SOCKETIO_MESSAGE',
  verb,
  channel,
  data,
  request: 'MESSAGE_REQUEST',
  success: 'MESSAGE_SUCCESS',
  failure: 'MESSAGE_FAILURE'
})

export const subscribe = (handler) => ({
  type: 'SUBSCRIBE',
  handler
})

export const unsubscribe = (handler) => ({
  type: 'UNSUBSCRIBE',
  handler
})

export const request = ({ method, endpoint, query, body, onSuccess, onFailure }) => ({
  type: 'API_REQUEST',
  method,
  endpoint,
  query,
  body,
  request: 'REQUEST_REQUEST',
  success: 'REQUEST_SUCCESS',
  failure: 'REQUEST_FAILURE',
  onSuccess,
  onFailure
})

export const presence = (handler) => ({
  type: 'PRESENCE',
  handler
})
