import { Page } from 'maha-admin'
import React from 'react'
import New from './new'

const mapPropsToPage = (props, context, resources, page) => ({
  title: 'Fields',
  rights: [],
  collection: {
    table: [
      { label: 'Label', key: 'label', primary: true }
    ],
    endpoint: `/api/admin/crm/${props.params.type}/fields`,
    empty: 'You have not yet created any fields',
    entity: 'field',
    icon: 'check-square-o',
    link: (record) => `/admin/crm/fields/${record.id}`,
    new: () => <New type={ props.params.type } />
  },
  task: {
    icon: 'plus',
    rights: [],
    modal: () => <New type={ props.params.type } />
  }
})

export default Page(null, mapPropsToPage)
