import { Avatar } from 'maha-admin'
import PropTypes from 'prop-types'
import moment from 'moment'
import Form from '../form'
import React from 'react'

class Timeline extends React.Component {

  static propTypes = {
    activities: PropTypes.array
  }

  render() {
    return (
      <div className="crm-timeline">
        <Form />
        { this._getActivities().map((activity, index) => [
          activity.newday ? <div className="crm-timeline-item" key={ `date_${index}` }>
            <div className="crm-timeline-item-rail" />
            <div className="crm-timeline-item-content">
              <div className="crm-timeline-date" key={ `date_${index}` }>
                { moment(activity.record.created_at).format('MMMM YYYY') }
              </div>
            </div>
          </div> : null,
          <div className="crm-timeline-item" key={ `event_${index}` }>
            <div className="crm-timeline-item-rail">
              <div className="crm-timeline-item-icon">
                <i className={ `fa fa-fw fa-${this._getIcon(activity.record)}` } />
              </div>
            </div>
            <div className="crm-timeline-item-content">
              <div className="crm-timeline-item-card">
                <div className="crm-timeline-item-card-header">
                  <div className="crm-timeline-item-card-header-avatar">
                    <Avatar user={ activity.record.user } width="28" />
                  </div>
                  <div className="crm-timeline-item-card-header-details">
                    <div className="crm-timeline-item-card-header-description">
                      <strong>{ activity.record.user.full_name }</strong> created a { activity.record.type }
                    </div>
                    <div className="crm-timeline-item-card-header-timestamp">
                      { moment(activity.record.created_at).format('MMM D @ h:mm A') }
                    </div>
                  </div>
                </div>
                <div className="crm-timeline-item-card-section">
                  <p dangerouslySetInnerHTML={{ __html: activity.record.content }} />
                </div>
              </div>
            </div>
          </div>
        ]) }
      </div>
    )
  }

  _getActivities() {
    const { activities } = this.props
    return activities.reduce((records, record) => {
      const newday = moment(record.created_at).format('M') !== moment(records.created_at).format('M')
      return {
        created_at: record.created_at,
        records: [
          ...records.records,
          {
            record,
            newday
          }
        ]
      }
    }, { records: [], created_at: null }).records
  }

  _getIcon(event) {
    if(event.type === 'call') return 'phone'
    if(event.type === 'note') return 'pencil'
    if(event.type === 'email') return 'envelope'
    if(event.type === 'task') return 'check'
  }

}

export default Timeline
