const INITIAL_STATE = {
  mode: null,
  active: null
}

export default (state = INITIAL_STATE, action) => {

  switch (action.type) {

  case 'SET_MODE':
    return {
      ...state,
      mode: action.mode
    }

  case 'CHOOSE':
    return {
      ...state,
      active: (action.index !== state.active) ? action.index : null
    }

  default:
    return state
  }

}
