import CompactReviewToken from './compact'
import PropTypes from 'prop-types'
import pluralize from 'pluralize'
import numeral from 'numeral'
import React from 'react'

const ReviewToken = ({ score, count }) => (
  <div className="maha-review">
    <h1>{ numeral(score).format('0.0') }</h1>
    <CompactReviewToken score={ score } />
    <div className="maha-review-summary">
      { pluralize('review', count, true) }
    </div>
  </div>
)

ReviewToken.propTypes = {
  score: PropTypes.number,
  count: PropTypes.number
}

export default ReviewToken
