import PropTypes from 'prop-types'
import React from 'react'

class Password extends React.Component {

  static contextTypes = {
    admin: PropTypes.object,
    flash: PropTypes.object
  }

  static propTypes = {
    question: PropTypes.object,
    team: PropTypes.object,
    token: PropTypes.string,
    user: PropTypes.object,
    onChangeMode: PropTypes.func,
    onPassword: PropTypes.func
  }

  password = null

  confirmation = null

  _handleSubmit = this._handleSubmit.bind(this)

  render() {
    return (
      <div className="maha-signin-panel">
        <div className="maha-signin-form">
          <div className="maha-signin-content">
            <h2>Choose a Password</h2>
            <p>Enter and confirm the password that you will use to signin.</p>
          </div>
          <form className="ui form" onSubmit={ this._handleSubmit }>
            <input type="hidden" value="something" />
            <div className="field text-field">
              <div className="ui left icon input">
                <i className="lock icon"></i>
                <input type="password" className="form-control" autoComplete="off" autoCapitalize="off" autoCorrect="off" spellCheck="false" placeholder="Password" ref={ (node) => this.password = node } />
              </div>
            </div>
            <div className="field text-field">
              <div className="ui left icon input">
                <i className="lock icon"></i>
                <input type="password" className="form-control" autoComplete="off" autoCapitalize="off" autoCorrect="off" spellCheck="false" placeholder="Confirm" ref={ (node) => this.confirmation = node } />
              </div>
            </div>
            <div className="field button-field">
              <button className={`ui fluid large ${(status === 'submitting') ? 'loading' : ''} button`}>
                Continue <i className="right chevron icon" />
              </button>
            </div>
          </form>
        </div>
      </div>
    )
  }

  _handleBack() {
    this.props.onChangeMode('answer')
  }

  _handleSubmit(e) {
    const { token, onPassword } = this.props
    this.password.focus()
    const password = this.password.value
    const confirmation = this.confirmation.value
    onPassword(token, password, confirmation)
    e.preventDefault()
    return false
  }

}

export default Password
