import PropTypes from 'prop-types'
import { Form } from 'maha-admin'
import React from 'react'

class New extends React.Component {

  static contextTypes = {
    modal: PropTypes.object
  }

  static propTypes = {
    type: PropTypes.string
  }

  _handleCancel = this._handleCancel.bind(this)
  _handleSuccess = this._handleSuccess.bind(this)

  render() {
    return <Form { ...this._getForm() } />
  }

  _getForm() {
    const { type } = this.props
    return {
      title: 'New Field',
      method: 'post',
      action: `/api/admin/crm/${type}/fields`,
      onCancel: this._handleCancel,
      onSuccess: this._handleSuccess,
      sections: [
        {
          fields: [
            { label: 'Label', name: 'label', type: 'textfield', required: true },
            { label: 'Input Type', name: 'input_type_id', type: 'lookup', endpoint: '/api/admin/crm/input_types', value: 'id', text: 'text' },
            { label: 'Is Required', name: 'is_required', type: 'checkbox' }
          ]
        }
      ]
    }
  }

  _handleCancel() {
    this.context.modal.close()
  }

  _handleSuccess(result) {
    this.context.modal.close()
  }

}

export default New
