import _ from 'lodash'

export const INITIAL_STATE = {
  status: 'uninitialized',
  routes: {},
  data: {}
}

export default (state = INITIAL_STATE, action) => {

  switch (action.type) {

  case 'FETCH_RESOURCE_REQUEST':
    return {
      ...state,
      status: state.status === 'uninitialized' ? 'loading' : 'refreshing',
      routes: {
        ...state.routes,
        [action.prop]: action.endpoint
      }
    }

  case 'FETCH_RESOURCE_SUCCESS':
    let data = {
      ...state.data,
      [action.prop]: action.result.data
    }
    let status = _.isEqual(Object.keys(data).sort(), Object.keys(state.routes).sort()) ? 'loaded' : state.status
    return {
      ...state,
      data,
      status
    }

  case 'FETCH_RESOURCE_FAILURE':
    return {
      ...state,
      status: (action.result.meta.status === 'FORBIDDEN') ? 'forbidden' : 'failure'
    }

  case 'CLEAR_RESOURCE':
    return {
      ...state,
      routes: _.omit(state.routes, action.prop),
      data: _.omit(state.data, action.prop)
    }

  default:
    return state

  }
}
