import React from 'react'
import PropTypes from 'prop-types'
import Avatar from '../avatar'
import _ from 'lodash'

class Accounts extends React.Component {

  static contextTypes = {
    admin: PropTypes.object,
    flash: PropTypes.object
  }

  static propTypes = {
    removing: PropTypes.bool,
    onChangeMode: PropTypes.func,
    onChooseAccount: PropTypes.func,
    onSet: PropTypes.func,
    onToggleRemove: PropTypes.func
  }

  _handleNew = this._handleNew.bind(this)
  _handleToggleRemove = this._handleToggleRemove.bind(this)

  render() {
    const { removing } = this.props
    const { teams } = this.context.admin
    return (
      <div className="maha-signin-panel">
        <div className="maha-signin-accounts">
          <div className="maha-signin-accounts-list">
            <div className="maha-signin-accounts-title">
              <h2>The Maha Platform</h2>
            </div>
            { teams.map((team, index) => {
              return (
                <div key={`account_${index}`} className="maha-signin-account" onClick={ this._handleClick.bind(this, index) } >
                  <div className="maha-signin-account-avatar">
                    <Avatar user={ team.user } />
                  </div>
                  <div className="maha-signin-account-details">
                    <p>
                      <strong>{ team.user.full_name }</strong><br />
                      { team.title}<br />
                      { !team.token && <em>Signed Out</em> }
                    </p>
                  </div>
                  { removing &&
                    <div className="maha-signin-account-remove">
                      <i className="fa fa-fw fa-times" />
                    </div>
                  }
                  { !removing &&
                    <div className="maha-signin-account-next">
                      <i className="fa fa-fw fa-chevron-right" />
                    </div>
                  }
                </div>
              )
            })}
            <div className="maha-signin-account">
              <div className="maha-signin-account-avatar">
                <div className="maha-avatar">
                  <div className="maha-avatar-badge">
                    <div className="maha-avatar-wrapper">
                      <i className="fa fa-fw fa-plus" />
                    </div>
                  </div>
                </div>
              </div>
              <div className="maha-signin-account-details" onClick={ this._handleNew }>
                <p>{ teams.length > 1 ? 'Sign in to another account' : 'Sign in to an account' }</p>
              </div>
              <div className="maha-signin-account-next">
                <i className="chevron right icon" />
              </div>
            </div>
            <div className="maha-signin-accounts-remove" onClick={ this._handleToggleRemove }>
              { removing ? 'Done' : 'Remove an account' }
            </div>
          </div>
        </div>
      </div>
    )
  }

  componentDidMount() {
    this.props.onSet(null, null, 'accounts')
  }

  _handleClick(index) {
    return this.props.removing ? this._handleRemove(index) : this._handleChoose(index)
  }

  _handleRemove(index) {
    if(this.context.admin.teams.length === 1) {
      this.props.onToggleRemove()
    }
    this.context.admin.removeTeam(index)

  }

  _handleChoose(index) {
    const { onChooseAccount } = this.props
    const { admin } = this.context
    const { teams } = admin
    if(teams[index].token) {
      return admin.chooseTeam(index)
    }
    const team = _.omit(teams[index], ['token'])
    const user = teams[index].user
    onChooseAccount(team, user)
  }

  _handleNew() {
    this.props.onChangeMode('team')
  }

  _handleToggleRemove() {
    if(this.context.admin.teams.length > 0) {
      this.props.onToggleRemove()
    }
  }

}

export default Accounts
