import _ from 'lodash'

export const INITIAL_STATE = {
  ignored: [],
  notifications: [],
  status: 'pending'
}

export default (state = INITIAL_STATE, action) => {

  switch (action.type) {

  case 'SET':
    return {
      ...state,
      ignored: action.ignored
    }

  case 'LOAD_REQUEST':
    return {
      ...state,
      status: 'loading'
    }

  case 'LOAD_FAILURE':
    return {
      ...state,
      status: 'failed'
    }

  case 'LOAD_SUCCESS':
    return {
      ...state,
      notifications: action.result.data,
      status: 'success'
    }

  case 'TOGGLE':
    return {
      ...state,
      ignored: _.includes(state.ignored, action.id) ? state.ignored.filter(id => id !== action.id) : [
        ...state.ignored,
        action.id
      ]
    }

  default:
    return state
  }

}
