import React from 'react'
import PropTypes from 'prop-types'
import { CompactUserToken, ModalPanel, Message, Search2 } from 'maha-admin'
import CompactProjectToken from '../compact_project_token'
import MembershipToken from '../membership_token'

class Membership extends React.Component {

  static contextTypes = {
    modal: PropTypes.object
  }

  static propTypes = {
    action: PropTypes.string,
    adding: PropTypes.bool,
    assigned: PropTypes.array,
    assignedEndpoint: PropTypes.string,
    ids: PropTypes.array,
    status: PropTypes.string,
    type: PropTypes.string,
    unassigned: PropTypes.array,
    unassignedEndpoint: PropTypes.string,
    values: PropTypes.array,
    onAdd: PropTypes.func,
    onBeginAdd: PropTypes.func,
    onChangeType: PropTypes.func,
    onFetchAssigned: PropTypes.func,
    onFetchMemberships: PropTypes.func,
    onFetchMemberTypes: PropTypes.func,
    onRemove: PropTypes.func,
    onSave: PropTypes.func
  }

  render() {
    const { assigned, type } = this.props
    return (
      <ModalPanel { ...this._getModalPanel() } >
        <div className={ this._getClass() }>
          <div className="maha-assignment-body">
            <div className="maha-assignment-assigned">
              <div className="maha-assignment-add" onClick={ this._handleBeginAdd.bind(this)}>
                Add a member...
              </div>
              { assigned.length === 0 && <Message { ...this._getEmpty() } /> }
              { assigned.length > 0 &&
                <div className="maha-assignment-list">
                  { assigned.map((membership, index) => (
                    <div className="maha-assignment-item" key={`member_${index}`}>
                      <div className="maha-assignment-item-token" onClick={ this._handleChangeType.bind(this, index) }>
                        <MembershipToken type={ type } membership={ membership } />
                      </div>
                      <div className="maha-assignment-item-icon" onClick={ this._handleRemove.bind(this, index) }>
                        <i className="fa fa-remove" />
                      </div>
                    </div>
                  ))}
                </div>
              }
            </div>
            <div className="maha-assignment-unassigned">
              <Search2 { ...this._getSearch() } />
            </div>
          </div>
        </div>
      </ModalPanel>
    )
  }

  componentDidMount() {
    const { assignedEndpoint, onFetchAssigned, onFetchMemberTypes } = this.props
    onFetchMemberTypes()
    onFetchAssigned(assignedEndpoint)
  }

  componentDidUpdate(prevProps) {
    const { status } = this.props
    const { modal } = this.context
    if(status !== prevProps.status && status === 'saved') modal.close()
  }

  _getClass() {
    const { adding } = this.props
    const classes = ['maha-assignment']
    if(adding) classes.push('adding')
    return classes.join(' ')
  }

  _getModalPanel() {
    const { type } = this.props
    const entities = type === 'user' ? 'Users' : 'Projects'
    return {
      title: `Manage ${entities}`,
      leftItems: [
        { label: 'Cancel', handler: this._handleCancel.bind(this) }
      ],
      rightItems: [
        { label: 'Save', handler: this._handleSave.bind(this) }
      ]
    }
  }

  _getEmpty() {
    const { type } = this.props
    const icon = type === 'user' ? 'users' : 'folder'
    const entities = type === 'user' ? 'users' : 'projects'
    return {
      icon,
      title: `No ${entities}`,
      text: `There are no ${entities}`
    }
  }

  _getSearch() {
    const { unassignedEndpoint, ids, type } = this.props
    const format = type === 'user' ? CompactUserToken : (project) => <CompactProjectToken project={ project } />
    const label = type === 'user' ? 'User' : 'Project'
    const text = type === 'user' ? 'full_name' : 'title'
    return {
      endpoint: unassignedEndpoint,
      exclude_ids: ids,
      format,
      label,
      text,
      value: 'id',
      onChoose: this._handleChoose.bind(this)
    }
  }

  _handleChoose(user) {
    const { type } = this.props
    this.props.onAdd(type, user)
  }

  _handleChangeType(index) {
    this.props.onChangeType(index)
  }

  _handleRemove(index) {
    this.props.onRemove(index)
  }

  _handleCancel() {
    this.context.modal.close()
  }

  _handleBeginAdd() {
    this.props.onBeginAdd()
  }

  _handleSave() {
    const { action, values } = this.props
    this.props.onSave(action, values)
  }

}

export default Membership
