import { ToggleList } from 'maha-admin'
import PropTypes from 'prop-types'
import React from 'react'

class Assignment extends React.Component {

  static contextTypes = {
    modal: PropTypes.object
  }

  static propTypes = {
    action: PropTypes.string,
    defaultValue: PropTypes.array,
    endpoint: PropTypes.string,
    filters: PropTypes.array,
    format: PropTypes.any,
    ids: PropTypes.array,
    status: PropTypes.string,
    title: PropTypes.string,
    text: PropTypes.string,
    value: PropTypes.string,
    onSave: PropTypes.func,
    onSet: PropTypes.func
  }

  _handleChange = this._handleChange.bind(this)

  render() {
    return (
      <ToggleList { ...this._getToggleList() } />
    )
  }

  componentDidUpdate(prevProps) {
    const { status } = this.props
    const { modal } = this.context
    if(prevProps.status !== status && status === 'success') {
      modal.close()
    }
  }

  _getToggleList() {
    const { defaultValue, endpoint, format, filters, text, value } = this.props
    return {
      defaultValue,
      endpoint,
      filters,
      format,
      text,
      value,
      onChange: this._handleChange
    }
  }

  _handleChange(ids) {
    this.props.onSet(ids)
  }

  _handleCancel() {
    this.context.modal.close()
  }

  _handleSave() {
    const { action, ids } = this.props
    this.props.onSave(action, { ids })
  }

}

export default Assignment
