import { CompactUserToken, Form } from 'maha-admin'
import PropTypes from 'prop-types'
import React from 'react'

class New extends React.Component {

  static contextTypes = {
    modal: PropTypes.object
  }

  static propTypes = {
    onCancel: PropTypes.func,
    onSuccess: PropTypes.func
  }

  _handleCancel = this._handleCancel.bind(this)
  _handleSuccess = this._handleSuccess.bind(this)

  render() {
    return <Form { ...this._getForm() } />
  }

  _getForm() {
    return {
      title: 'New Channel',
      method: 'post',
      action: '/api/admin/chat/channels',
      onCancel: this._handleCancel,
      onSuccess: this._handleSuccess,
      saveText: 'Create',
      sections: [
        {
          fields: [
            { label: 'Name', name: 'name', type: 'textfield', placeholder: 'Give this channel a name' },
            { label: 'Description', name: 'description', type: 'textfield', placeholder: 'Describe the purpose of this channel' },
            { label: 'Members', name: 'ids', type: 'lookup2', required: true, multiple: true, endpoint: '/api/admin/users', filters: { app_id: { $eq: 6 } }, value: 'id', text: 'full_name', format: CompactUserToken, placeholder: 'Add members to this channel' }
          ]
        }
      ]
    }
  }

  _handleCancel() {
    this.props.onCancel()
  }

  _handleSuccess(result) {
    this.props.onSuccess(result)
  }

}

export default New
