import React from 'react'
import PropTypes from 'prop-types'
import _ from 'lodash'
import Loader from '../loader'

class Admin extends React.Component {

  static childContextTypes = {
    admin: PropTypes.object
  }

  static contextTypes = {
    flash: PropTypes.object,
    logger: PropTypes.object,
    network: PropTypes.object,
    router: PropTypes.object
  }

  static propTypes = {
    active: PropTypes.number,
    apps: PropTypes.array,
    children: PropTypes.any,
    fingerprint: PropTypes.string,
    presence: PropTypes.array,
    sessions: PropTypes.array,
    session: PropTypes.object,
    status: PropTypes.string,
    teams: PropTypes.array,
    team: PropTypes.object,
    unseen: PropTypes.number,
    user: PropTypes.object,
    onAddTeam: PropTypes.func,
    onChooseTeam: PropTypes.func,
    onLoadAdmin: PropTypes.func,
    onLoadSession: PropTypes.func,
    onRemoveSession: PropTypes.func,
    onRemoveTeam: PropTypes.func,
    onSaveAdmin: PropTypes.func,
    onSetPresence: PropTypes.func,
    onSignin: PropTypes.func,
    onSignout: PropTypes.func,
    onUpdateSession: PropTypes.func,
    onUpdateUnseen: PropTypes.func
  }

  state = {
    host: 'browser',
    path: null,
    search: null,
    hash: null
  }

  _handleAddTeam = this._handleAddTeam.bind(this)
  _handleBlurFocus = this._handleBlurFocus.bind(this)
  _handleCheckAccess = this._handleCheckAccess.bind(this)
  _handleChooseTeam = this._handleChooseTeam.bind(this)
  _handleForcedSignout = this._handleForcedSignout.bind(this)
  _handlePresence = this._handlePresence.bind(this)
  _handleReloadSession = this._handleReloadSession.bind(this)
  _handleRemoveTeam = this._handleRemoveTeam.bind(this)
  _handleSignin = this._handleSignin.bind(this)
  _handleSignout = this._handleSignout.bind(this)
  _handleUpdateUnseen = this._handleUpdateUnseen.bind(this)

  render() {
    const { status } = this.props
    if(status === 'loading') return <Loader />
    return this.props.children
  }

  componentDidMount() {
    const { router } = this.context
    const { pathname, search, hash } = this.context.router
    this.setState({ host: this._getHost() })
    this.props.onLoadAdmin()
    if(pathname.match(/\/(activate|signin|reset).*/)) return
    this.setState({ pathname, search, hash })
    router.replace('/admin')
    window.addEventListener('blur', this._handleBlurFocus, false)
    window.addEventListener('focus', this._handleBlurFocus, false)
  }

  componentDidUpdate(prevProps) {
    const { active, fingerprint, teams, team, unseen, user, onLoadSession, onSaveAdmin } = this.props
    const { router } = this.context
    const { pathname, search, hash } = this.state
    if(_.get(prevProps, 'team.user.id') !== _.get(team, 'user.id')) {
      if(!_.isNil(prevProps.team)) this._closeSockets(prevProps.team)
      if(!_.isNil(team)) onLoadSession(team.token, fingerprint)
    }
    if(!_.isEqual(prevProps.teams, teams)) {
      const reservedPath = ['activate','reset','signin'].reduce((reserved, path) => {
        return reserved || router.pathname.startsWith(`/admin/${path}`)
      }, false)
      if(!team && !reservedPath) router.replace('/admin/signin')
      if(!user) onSaveAdmin({ active, fingerprint, teams })
    }
    if(!_.isEqual(prevProps.user, user)) {
      onSaveAdmin({ active, fingerprint, teams })
    }
    if(_.get(prevProps, 'user.id') !== _.get(user, 'id') && !_.isNil(user)) {
      this.context.logger.login(user)
      this._openSockets(team)
      if(router.pathname.match(/\/(activate|signin|reset).*/)) return
      if(pathname) {
        setTimeout(() => router.push({ pathname, search, hash }), 250)
        this.setState({ pathname: null, search: null, hash: null })
      }
    }
    if(unseen !== prevProps.unseen) {
      const count = unseen < 10 ? unseen : 'x'
      const link = document.getElementById('favicon')
      link.type = 'image/x-icon'
      link.rel = 'shortcut icon'
      link.href = `/admin/images/icons/favicon-${count}.png`
      document.getElementsByTagName('head')[0].appendChild(link)
    }
  }

  componentWillUnmount() {
    window.removeEventListener('blur', this._handleBlurFocus)
    window.removeEventListener('focus', this._handleBlurFocus)
  }

  getChildContext() {
    const { apps, active, teams, team, unseen, user } = this.props
    return {
      admin: {
        apps,
        active,
        teams,
        team,
        unseen,
        user,
        addTeam: this._handleAddTeam,
        checkAccess: this._handleCheckAccess,
        chooseTeam: this._handleChooseTeam,
        removeTeam: this._handleRemoveTeam,
        signin: this._handleSignin,
        signout: this._handleSignout,
        updateUnseen: this._handleUpdateUnseen
      }
    }
  }

  _getHost() {
    if(navigator.userAgent.search('Cordova') >= 0) {
      return 'cordova'
    } else if(navigator.userAgent.search('Electron') >= 0) {
      return 'electron'
    } else {
      return 'browser'
    }
  }

  _openSockets(team) {
    const { network } = this.context
    network.join([
      `/admin/users/${team.user.id}`,
      `/admin/teams/${team.id}`
    ])
    network.subscribe([
      { action: 'session', handler: this._handleReloadSession },
      { action: 'signout', handler: this._handleForcedSignout },
      { action: 'presence', handler: this._handlePresence }
    ])
    network.signin({
      status: document.hasFocus() ? 'active' : 'absent'
    })
  }

  _closeSockets(team) {
    const { network } = this.context
    network.leave([
      `/admin/users/${team.user.id}`,
      `/admin/teams/${team.id}`
    ])
    network.unsubscribe([
      { action: 'session', handler: this._handleReloadSession },
      { action: 'signout', handler: this._handleForcedSignout },
      { action: 'presence', handler: this._handlePresence }
    ])
    network.signout()
  }

  _handleForcedSignout() {
    const { flash } = this.context
    this._handleSignout()
    flash.set('info', 'Your account has been signed out of all devices')
  }

  _handleAddTeam(team, token, user) {
    this.props.onAddTeam(team, token, user)
  }

  _handleBlurFocus() {
    const { team } = this.props
    if(!team) return
    this.context.network.emit('status', {
      status: document.hasFocus() ? 'active' : 'absent'
    })
  }

  _handleCheckAccess(code) {
    return (_.findIndex(this.props.apps, { code }) >= 0)
  }

  _handleChooseTeam(index) {
    const { router } = this.context
    const { active, onChooseTeam } = this.props
    if(index === active) {
      router.push('/admin')
    } else {
      onChooseTeam(index)
    }
  }

  _handleRemoveTeam(index) {
    this.props.onRemoveTeam(index)
  }

  _handleSignout() {
    const { active, onRemoveSession, onSignout } = this.props
    onRemoveSession()
    onSignout(active)
  }

  _handleSignin(index, token) {
    this.props.onSignin(index, token)
  }

  _handlePresence(presence) {
    this.props.onSetPresence(presence)
  }

  _handleReloadSession() {
    const { team, onLoadSession } = this.props
    onLoadSession(team.token)
  }

  _handleUpdateUnseen(difference) {
    this.props.onUpdateUnseen(difference)
  }

}

export default Admin
