import _ from 'lodash'

export const INITIAL_STATE = {
  uploads: []
}

const reducer = (state = INITIAL_STATE, action) => {

  switch (action.type) {

  case 'PROGRESS':
    return {
      ...state,
      progress: action.progress
    }

  case 'ADD_UPLOAD':
    return {
      ...state,
      uploads: [
        ...state.uploads,
        {
          ...action.file,
          status: 'added',
          progress: 0,
          asset: null
        }
      ]
    }

  case 'UPDATE_UPLOAD_PROGRESS':
    return {
      ...state,
      uploads: state.uploads.map(upload => (upload.file.uniqueIdentifier === action.uniqueIdentifier) ? {
        ...upload,
        status: 'uploading',
        progress: Math.floor(action.progress * 100)
      } : upload)
    }
  
  case 'UPDATE_UPLOAD':
    return {
      ...state,
      uploads: state.uploads.map(upload => (upload.file.uniqueIdentifier === action.uniqueIdentifier) ? {
        ...upload,
        ...action.meta
      } : upload)
    }

  case 'REMOVE_UPLOAD':
    const index = _.findIndex(state.uploads, { uniqueIdentifier: action.file.uniqueIdentifier })
    return {
      ...state,
      uploads: [
        ...state.uploads.slice(0, index),
        ...state.uploads.slice(index + 1)
      ]
    }

  default:
    return state

  }

}

export default reducer
