import { Form, Checkbox } from 'maha-admin'
import { connect } from 'react-redux'
import PropTypes from 'prop-types'
import React from 'react'

class Push extends React.Component {

  static contextTypes = {
    host: PropTypes.object
  }

  static defaultProps = {
    push: PropTypes.string
  }

  state = {
    value: null
  }

  _handleClick = this._handleClick.bind(this)
  _handleToggle = this._handleToggle.bind(this)

  render() {
    const { value } = this.state
    return (
      <div className="push-section">
        { value != null && window.Notification && navigator.serviceWorker &&
          <div>
            <label>Push Notifications</label>
            <div className={ `toggle-row ${(value) ? 'granted' : 'not-granted'}` } onClick={ this._handleClick }>
              { value && <i className="fa fa-check-circle" /> }
              { !value && <Checkbox { ...this._getCheckbox() } /> }
              <div className="text">
                { value && "Great, you've got push notifications enabled!" }
                { !value && "Uh oh, you aren't getting desktop notifications! Click here to enable them." }
              </div>
            </div>
          </div>
        }
      </div>
    )
  }

  componentDidMount(){
    if( this.props.push ){
      this._updatePushValue()
    }
  }

  componentDidUpdate(prevProps){
    if( prevProps.push != this.props.push && prevProps.push != '' ){
      this._updatePushValue()
    }
  }

  _updatePushValue(){
    const { push } = this.props
    if( push === 'unknown' || push === 'never' ){
      this.setState({ value: false })
    } else {
      this.setState({ value: true })
    }
  }

  _getCheckbox(){
    const { push } = this.props
    return {
      defaultValue: this.state.value,
      disabled: (push != 'granted') ? false : true
    }
  }

  _handleClick(e) {
    this._handleToggle()
    e.stopPropagation()
  }

  _handleToggle(e) {
    const { host } = this.context
    const { push } = this.props
    if( push != 'granted' ){
      host.promptNotifications()
    }
  }

}

const mapStateToProps = (state, props) => ({
  push: state.maha.browser.push
})

export default connect(mapStateToProps)(Push)
