import React from 'react'
import PropTypes from 'prop-types'
import { Form } from 'maha-admin'

class Password extends React.Component {

  static contextTypes = {
    flash: PropTypes.object,
    modal: PropTypes.object
  }

  _handleCancel = this._handleCancel.bind(this)
  _handleSuccess = this._handleSuccess.bind(this)

  render() {
    return <Form { ...this._getForm() } />
  }

  _getForm() {
    return {
      title: 'Change Password',
      method: 'patch',
      action: '/api/admin/account/password',
      onCancel: this._handleCancel,
      onSuccess: this._handleSuccess,
      sections: [
        {
          fields: [
            { label: 'Old Password', name: 'old_password', type: 'password', placeholder: 'Old Password', required: true },
            { label: 'New Password', name: 'password', type: 'password', placeholder: 'New Password', required: true },
            { label: 'Confirm Password', name: 'confirmation', type: 'password', placeholder: 'Confirm Password', required: true }
          ]
        }
      ]
    }
  }

  _handleCancel() {
    this.context.modal.close()
  }

  _handleSuccess(user) {
    this.context.modal.close()
    this.context.flash.set('success', 'Your password was successfully changed')
  }

}

export default Password
