export const INITIAL_STATE = {
  q: '',
  article: null
}

export default (state = INITIAL_STATE, action) => {

  switch (action.type) {

  case 'TYPE':
    return {
      ...state,
      q: action.q
    }

  case 'FETCH_SUCCESS':
    return {
      ...state,
      article: action.result.data.content
    }

  default:
    return state

  }
}
