import { convertToRaw, CompositeDecorator, ContentState, Editor, EditorState, getDefaultKeyBinding, convertFromHTML } from 'draft-js'
import PropTypes from 'prop-types'
import React from 'react'

class RichEditor extends React.Component {

  static propTypes = {
    defaultValue: PropTypes.string,
    editorState: PropTypes.object,
    placeholder: PropTypes.string,
    uploads: PropTypes.array,
    onChange: PropTypes.func,
    onFocus: PropTypes.func,
    onReady: PropTypes.func,
    onSetState: PropTypes.func,
    onSubmit: PropTypes.func,
    onToggleEmojis: PropTypes.func,
    onUpArrow: PropTypes.func
  }

  static defaultProps = {
    placeholder: 'Enter text',
    onChange: () => {},
    onReady: () => {},
    onSubmit: () => {},
    onUpArrow: () => {},
    onFocus: () => {}
  }

  _handleChange = this._handleChange.bind(this)
  _handleKeyBinding = this._handleKeyBinding.bind(this)
  _handleKeyCommand = this._handleKeyCommand.bind(this)
  _handleFocus = this._handleFocus.bind(this)
  _handleUpArrow = this._handleUpArrow.bind(this)

  render() {
    const { editorState } = this.props
    return (
      <div className="maha-editor">
        { editorState && <Editor { ...this._getEditor() } /> }
      </div>
    )
  }

  componentDidMount() {
    const { defaultValue } = this.props
    const editorState = this._getInitialState(defaultValue)
    this._handleChange(editorState)
    this.props.onReady()
  }

  _getInitialState(value) {
    const decorator = this._getDecorator()
    if(!value) return EditorState.createEmpty(decorator)
    const blocksFromHTML = convertFromHTML(value.replace(/\n/g, '<br />'))
    const contentState = ContentState.createFromBlockArray(blocksFromHTML)
    const currentState = EditorState.createWithContent(contentState, decorator)
    const selectionState = EditorState.moveSelectionToEnd(currentState).getSelection()
    return EditorState.forceSelection(currentState, selectionState)
  }

  _getDecorator() {
    return new CompositeDecorator([])
  }

  _getEditor() {
    const { editorState, placeholder } = this.props
    return {
      ref: node => this.el = node,
      editorState: editorState,
      onChange: this._handleChange,
      placeholder,
      keyBindingFn: this._handleKeyBinding,
      handleKeyCommand: this._handleKeyCommand,
      onFocus: this._handleFocus,
      onUpArrow: this._handleUpArrow
    }
  }

  _handleReset() {
    const { editorState } = this.props
    const resetEditorState = EditorState.push(editorState, ContentState.createFromText(''))
    this._handleChange(EditorState.moveFocusToEnd(resetEditorState))
  }

  _handleChange(editorState) {
    const { onChange } = this.props
    this.props.onSetState(editorState)
    const converted = this._getConverted(editorState)
    onChange(converted)
  }

  _handleKeyBinding(e) {
    // if(e.keyCode === 13 && !e.nativeEvent.shiftKey) {
    //   return 'message-save'
    // }
    return getDefaultKeyBinding(e)
  }

  _handleKeyCommand(command) {
    if (command === 'message-save') {
      this._handleSave()
      return 'handled'
    }
    return 'not-handled'
  }

  _handleUpArrow(e) {
    e.preventDefault()
    this.props.onUpArrow()
  }

  _handleFocus() {
    this.props.onFocus()
  }

  _getConverted(editorState) {
    const state = convertToRaw(editorState.getCurrentContent())
    return state.blocks.reduce((content, block) => [
      ...content,
      block.text
    ], []).join('\r\n')
  }

  _handleSave() {
    const { editorState, onSubmit } = this.props
    const converted = this._getConverted(editorState)
    if(converted.length === 0) return
    onSubmit(converted)
    this._handleReset()
  }

}

export default RichEditor
