import PropTypes from 'prop-types'
import React from 'react'
import _ from 'lodash'

class Network extends React.Component {

  static childContextTypes = {
    network: PropTypes.object
  }

  static contextTypes = {
    alerts: PropTypes.object
  }

  static propTypes = {
    channels: PropTypes.object,
    handlers: PropTypes.array,
    children: PropTypes.any,
    online: PropTypes.bool,
    onConnect: PropTypes.func,
    onDisconnect: PropTypes.func,
    onEmit: PropTypes.func,
    onInit: PropTypes.func,
    onJoin: PropTypes.func,
    onLeave: PropTypes.func,
    onMessage: PropTypes.func,
    onPresence: PropTypes.func,
    onRequest: PropTypes.func,
    onSubscribe: PropTypes.func,
    onUnsubscribe: PropTypes.func
  }

  _handleConnect = this._handleConnect.bind(this)
  _handleDisconnect = this._handleDisconnect.bind(this)
  _handleEmit = this._handleEmit.bind(this)
  _handleJoin = this._handleJoin.bind(this)
  _handleLeave = this._handleLeave.bind(this)
  _handlePresence = this._handlePresence.bind(this)
  _handleReceiveMessage = this._handleReceiveMessage.bind(this)
  _handleRequest = this._handleRequest.bind(this)
  _handleSendMessage = this._handleSendMessage.bind(this)
  _handleSignin = this._handleSignin.bind(this)
  _handleSignout = this._handleSignout.bind(this)
  _handleSubscribe = this._handleSubscribe.bind(this)
  _handleUnsubscribe = this._handleUnsubscribe.bind(this)

  render() {
    return this.props.children
  }

  componentDidMount() {
    const connect = this._handleConnect
    const disconnect = this._handleDisconnect
    const message = this._handleReceiveMessage
    const presence = this._handlePresence
    this.props.onInit(connect, disconnect, message, presence)
  }

  componentDidUpdate(prevProps) {
    const { online } = this.props
    if(online !== prevProps.online) {
      if(prevProps.online === true) this._handleOfflineAlert()
      if(prevProps.online === false) this._handleOnlineAlert()
    }
  }

  getChildContext() {
    return {
      network: {
        emit: this._handleEmit,
        join: this._handleJoin,
        leave: this._handleLeave,
        message: this._handleSendMessage,
        request: this._handleRequest,
        signin: this._handleSignin,
        signout: this._handleSignout,
        subscribe: this._handleSubscribe,
        unsubscribe: this._handleUnsubscribe
      }
    }
  }

  _getHandlers(target, action) {
    const targetHandlers = _.filter(this.props.handlers, { target, action })
    if(targetHandlers.length > 0) return targetHandlers
    const actionHandlers = _.filter(this.props.handlers, { action })
    if(actionHandlers.length > 0) return actionHandlers
    return null
  }

  _handleConnect() {
    this.props.onConnect()
    if(this.props.online === null) return
    this._handleSignin({
      status: document.hasFocus() ? 'active' : 'absent'
    })
    setTimeout(() => this._handleJoin(Object.keys(this.props.channels)), 500)
  }

  _handleOnlineAlert() {
    const { alerts } = this.context
    alerts.pop()
    alerts.push({
      priority: 1,
      color: 'green',
      message: 'You\'re back online!'
    })
    setTimeout(alerts.pop, 2500)
  }

  _handleDisconnect() {
    this.props.onDisconnect()
  }

  _handleOfflineAlert() {
    this.context.alerts.push({
      priority: 1,
      color: 'yellow',
      message: 'Unable to connect to the server. We\'ll keep trying to reconnect.'
    })
  }

  _handleJoin(channels) {
    _.castArray(channels).map(channel => {
      this.props.onJoin(channel)
    })
  }

  _handleLeave(channels) {
    _.castArray(channels).map(channel => {
      this.props.onLeave(channel)
    })
  }

  _handleSubscribe(handlers) {
    _.castArray(handlers).map((handler) => {
      this.props.onSubscribe(handler)
    })
  }

  _handleUnsubscribe(handlers) {
    _.castArray(handlers).map((handler) => {
      this.props.onUnsubscribe(handler)
    })
  }

  _handleSendMessage({ channel, target, action, data }) {
    this.props.onMessage('message', channel, {
      target: target || channel,
      action,
      data
    })
  }

  _handleEmit(verb, data) {
    this.props.onMessage(verb, null, data)
  }

  _handlePresence(presence) {
    const handlers = this._getHandlers(null, 'presence')
    if(!handlers) return
    handlers.map(handler => handler.handler(presence))
  }

  _handleReceiveMessage(message) {
    const { target, action } = message
    const handlers = this._getHandlers(target, action)
    if(!handlers) return
    handlers.map(handler => handler.handler(message.data))
  }

  _handleRequest(request) {
    const { onRequest } = this.props
    onRequest(request)
  }

  _handleSignin(data) {
    this._handleEmit('signin', data)
  }

  _handleSignout() {
    this._handleEmit('signout')
  }

}

export default Network
