import Timeline from '../../components/timeline'
import { List, Page } from 'maha-admin'
import PropTypes from 'prop-types'
import React from 'react'
import Edit from './edit'

const Details = ({ contact, appUserValues }) => {

  const list = {
    sections: [
      {
        items: [
        ]
      }
    ]
  }

  return <List { ...list } />

}

Details.propTypes = {
  appUserValues: PropTypes.array,
  contact: PropTypes.object
}

const mapResourcesToPage = (props, context) => ({
  organization: `/api/admin/crm/organizations/${props.params.id}`,
  activities: `/api/admin/crm/organizations/${props.params.id}/activities`,
  fields: '/api/admin/crm/organizations/fields'
})

const mapPropsToPage = (props, context, resources) => ({
  title: resources.organization.name,
  rights: [],
  tabs: {
    items: [
      { label: 'Activity', component: <Timeline activities={ resources.activities } /> }
    ]
  },
  tasks: {
    items: [
      {
        label: 'Edit Contact',
        modal: (props) => <Edit contact={ resources.contact } />
      }
    ]
  }
})

export default Page(mapResourcesToPage, mapPropsToPage)
