import { Avatar, List, Page } from 'maha-admin'
import PropTypes from 'prop-types'
import Timeline from '../../components/timeline'
import React from 'react'
import Edit from './edit'

const Details = ({ contact, fields, appUserValues }) => {

  const list = {
    sections: [
      {
        items: [
          { label: 'Email', content: contact.email },
          { label: 'Phone', content: contact.phone },
          ...fields.map((field, index) => ({
            label: field.label, content: contact.values[field.code]
          }))
        ]
      }
    ]
  }

  return <List { ...list } />

}

Details.propTypes = {
  appUserValues: PropTypes.array,
  fields: PropTypes.array,
  contact: PropTypes.object
}

const mapResourcesToPage = (props, context) => ({
  contact: `/api/admin/crm/contacts/${props.params.id}`,
  activities: `/api/admin/crm/contacts/${props.params.id}/activities`,
  fields: '/api/admin/crm/contacts/fields'
})

const mapPropsToPage = (props, context, resources) => ({
  title: resources.contact.full_name,
  rights: [],
  tabs: {
    header: <Avatar user={ resources.contact } width="120" />,
    items: [
      { label: 'Details', component: <Details contact={ resources.contact } fields={ resources.fields } /> },
      { label: 'Activity', component: <Timeline activities={ resources.activities } /> }
    ]
  },
  tasks: {
    items: [
      {
        label: 'Edit Contact',
        modal: (props) => <Edit contact={ resources.contact } fields={ resources.fields } />
      }
    ]
  }
})

export default Page(mapResourcesToPage, mapPropsToPage)
