import { Carousel } from 'maha-admin'
import PropTypes from 'prop-types'
import Slide from './slide'
import React from 'react'

class Tour extends React.Component {

  static contextTypes = {
    modal: PropTypes.object
  }

  static propTypes = {
    color: PropTypes.string,
    slides: PropTypes.array
  }

  static defaultProps = {
    color: 'green'
  }

  render() {
    const { color } = this.props
    return (
      <div className={`maha-tour ${color}`}>
        <Carousel { ...this._getCarousel() } />
        <div className="maha-tour-footer" onClick={ this._handleClose.bind(this) }>
          Skip Tour
        </div>
      </div>
    )

  }

  _getCarousel() {
    const { slides } = this.props
    return {
      infinite: false,
      slides: slides.map((slide, index) => <Slide key={`slide_${index}`} { ...slide } />)
    }
  }

  _handleClose() {
    this.context.modal.close()
  }

}

export default Tour
