import React from 'react'
import PropTypes from 'prop-types'
import { Form } from 'maha-admin'

class EmailTemplatesEdit extends React.Component {

  static contextTypes = {
    modal: PropTypes.object
  }

  static propTypes = {
    emailTemplate: PropTypes.object
  }

  _handleCancel = this._handleCancel.bind(this)
  _handleSuccess = this._handleSuccess.bind(this)

  render() {
    return <Form { ...this._getForm() } />
  }

  _getForm() {
    const { emailTemplate } = this.props
    return {
      title: 'Edit Email Template',
      method: 'patch',
      endpoint: `/api/admin/team/email_templates/${emailTemplate.id}`,
      action: `/api/admin/team/email_templates/${emailTemplate.id}`,
      onCancel: this._handleCancel,
      onSuccess: this._handleSuccess,
      sections: [
        {
          fields: [
            { label: 'Subject', name: 'subject', type: 'textfield' },
            { label: 'Body', name: 'body', type: 'textarea', rows: 12 }
          ]
        }
      ]
    }
  }

  _handleCancel() {
    this.context.modal.close()
  }

  _handleSuccess() {
    this.context.modal.close()
  }

}

export default EmailTemplatesEdit
