import { CSSTransition } from 'react-transition-group'
import { Button, Collection, Tabs, Panel, ModalPanel } from 'reframe'
import PropTypes from 'prop-types'
import Helmet from 'react-helmet'
import React from 'react'
import _ from 'lodash'
import { connect } from 'react-redux'

export default (pageResources, pageProps) => {

  class Page extends React.Component {

    static contextTypes = {
      admin: PropTypes.object,
      alert: PropTypes.object,
      confirm: PropTypes.object,
      drawer: PropTypes.object,
      flash: PropTypes.object,
      logger: PropTypes.object,
      network: PropTypes.object,
      notifications: PropTypes.object,
      modal: PropTypes.object,
      platform: PropTypes.object,
      prompt: PropTypes.object,
      router: PropTypes.object,
      stack: PropTypes.object,
      tasks: PropTypes.object
    }

    static childContextTypes = {
      page: PropTypes.object
    }

    static propTypes = {
      buttons: PropTypes.array,
      collection: PropTypes.object,
      data: PropTypes.object,
      panel: PropTypes.object,
      params: PropTypes.object,
      pathname: PropTypes.string,
      status: PropTypes.string,
      tabs: PropTypes.object,
      unseen: PropTypes.number,
      onFetchResource: PropTypes.func
    }

    state = {
      access: true,
      cacheKey: null,
      pathname: null,
      ready: false
    }

    _handleBack = this._handleBack.bind(this)
    _handleCheckAccess = this._handleCheckAccess.bind(this)
    _handleFetchResources = this._handleFetchResources.bind(this)
    _handleRefreshResources = this._handleRefreshResources.bind(this)
    _handleTasks = this._handleTasks.bind(this)
    _handleTask = this._handleTask.bind(this)

    render() {
      const { access, ready } = this.state
      const { data, status } = this.props
      const resources = this.resources()
      const loaded = !resources || _.isEqual(Object.keys(data).sort(), Object.keys(resources).sort())
      const { buttons, collection, component, panel, tabs, title } = (loaded) ? this.page() : {}
      const Component = component
      return (
        <ModalPanel { ...this._getPanel() }>
          <div className="maha-page-body">
            { title && <Helmet  title={ this._getTitle(title) } /> }
            { access && resources && status === 'loading' &&
              <div className="maha-loader">
                <div className="ui active inverted dimmer">
                  <div className="ui large text loader">Loading</div>
                </div>
              </div>
            }
            { access && resources && status === 'failure' &&
              <div className="maha-error">
                <div className="maha-error-message">
                  <i className="warning sign icon" />
                  <h2>Unable to load<br /> this page</h2>
                  <button className="ui basic red button" onClick={ this._handleRefreshResources }>Try again</button>
                </div>
              </div>
            }
            { access && ready && loaded && component && <Component { ...this.props } {...data} /> }
            { access && ready && loaded && collection && <Collection { ...this._getCollection() } /> }
            { access && ready && loaded && tabs && <Tabs { ...tabs } /> }
            { access && ready && loaded && panel && <Panel { ...panel } /> }
            { (!access || status === 'forbidden') &&
              <div className="maha-error">
                <div className="maha-error-message">
                  <i className="warning sign icon" />
                  <h2>You do not have permission to access this content</h2>
                </div>
              </div>
            }
          </div>
          <CSSTransition in={ !_.isNil(buttons) } classNames="expanded" timeout={ 150 } mountOnEnter={ true } unmountOnExit={ true }>
            <div className="maha-page-footer">
              <div className="maha-page-footer-items">
                { buttons && buttons.map((button, index) => (
                  <div key={`pager_footer_${index}`} className="maha-page-footer-item">
                    <Button { ...button } />
                  </div>
                )) }
              </div>
            </div>
          </CSSTransition>
        </ModalPanel>
      )
    }

    componentDidMount() {
      const { router } = this.context
      const { pathname } = router
      this.setState({ pathname })
      setTimeout(() => this.setState({ ready: true }), 500)
      this._handleJoinChannel(pathname)
    }

    componentDidUpdate(prevProps, prevState) {
      const { status } = this.props
      if(prevState.ready !== this.state.ready) {
        const resources = this.resources()
        if(resources) {
          this._handleFetchResources(resources)
        } else {
          this._checkPageAccess()
        }
      }
      if(prevProps.status !== status && status === 'loaded') {
        this._checkPageAccess()
      }
    }

    shouldComponentUpdate(nextProps, nextState) {
      if(nextState.access !== this.state.access) return true
      if(nextState.ready !== this.state.ready) return true
      if(nextState.cacheKey !== this.state.cacheKey) return true
      if(nextState.status !== this.props.status) return true
      if(!_.isEqual(nextProps.data, this.props.data)) return true
      return false
    }

    componentWillUnmount() {
      const { pathname } = this.state
      this._handleLeaveChannel(pathname)
    }

    componentDidCatch(error, info) {
      this.context.logger.error(error, info)
    }

    getChildContext() {
      return {
        page: {
          fetch: this._handleFetchResources,
          refresh: this._handleRefreshResources,
          params: this.props.params,
          pathname: this.props.pathname
        }
      }
    }

    resources() {
      const resources = pageResources ? pageResources(this.props, this.context) : {}
      return (!_.isEmpty(resources)) ? resources : null
    }

    page() {
      if(!pageProps) return null
      const page = this.getChildContext().page
      const computed = pageProps ? pageProps(this.props, this.context, this.props.data, page) : {}
      const tasks = computed.tasks && computed.tasks.items ? computed.tasks.items.filter(this._handleCheckAccess) : []
      const task = (computed.task && this._handleCheckAccess(computed.task)) ? computed.task : null
      return {
        ...computed,
        tasks: tasks && tasks.length > 0 ? { ...computed.tasks, items: tasks } : null,
        task
      }
    }

    refresh() {
      this._handleRefreshResources()
    }

    _getTitle(pageTitle) {
      const { unseen } = this.props
      const { team } = this.context.admin
      const badge = unseen > 0 ? ` (${unseen})` : ''
      const title = pageTitle || team.title
      return title + badge
    }

    _getPanel() {
      const { data } = this.props
      const resources = this.resources()
      const loaded = !resources || _.isEqual(Object.keys(data).sort(), Object.keys(resources).sort())
      const { className, color, task, tasks, title } = (loaded) ? this.page() : {}
      const panel = {
        className: `maha-page ${color || ''} ${className || ''}`,
        title,
        leftItems: [
          { icon: 'chevron-left', handler: this._handleBack }
        ]
      }
      if(tasks && tasks.items && tasks.items.filter(task => task.show !== false).length > 0) {
        panel.rightItems = [
          { icon: tasks.icon || 'ellipsis-v', handler: this._handleTasks }
        ]
      }
      if(task) {
        panel.rightItems = [
          { icon: task.icon || 'plus', handler: this._handleTask }
        ]
      }
      return panel
    }

    _getCollection() {
      const { collection } = this.page()
      const { cacheKey } = this.state
      const token = this.context.admin.team.token
      return {
        cacheKey,
        token,
        ...collection
      }
    }

    _checkPageAccess() {
      const access = this._handleCheckAccess(this.page())
      this.setState({ access })
    }

    _handleCheckAccess(options) {
      const { user, team } = this.context.admin
      if(options.rights && !this._userHasRight(user, options.rights)) {
        return false
      }
      if(options.access) {
        return options.access(user, team.token).then(result => !result)
      }
      return true
    }

    _handleFetchResources(resources) {
      const { onFetchResource } = this.props
      _.forOwn(resources, (endpoint, prop) => {
        onFetchResource(prop, endpoint)
      })
    }

    _handleRefreshResources(props = null) {
      this.setState({ cacheKey: _.random(100000, 999999).toString(36) })
      const resources = this.resources()
      if(!resources) return
      if(!props) props = Object.keys(resources)
      if(!_.isArray(props)) props = [props]
      Object.keys(resources).filter(prop => {
        return _.includes(props, prop)
      }).map(prop => {
        this.props.onFetchResource(prop, resources[prop])
      })
    }

    _userHasRight(user, rights) {
      if(!rights) return true
      return rights.reduce((permit, right) => {
        return (!_.includes(user.rights, right)) ? false : permit
      }, true)
    }

    _handleJoinChannel(channel) {
      const { network } = this.context
      network.join(channel)
      network.subscribe({
        target: channel,
        action: 'refresh',
        handler: this._handleRefreshResources
      })
    }

    _handleLeaveChannel(channel) {
      const { network } = this.context
      network.leave(channel)
      network.unsubscribe({
        target: channel,
        action: 'refresh',
        handler: this._handleRefreshResources
      })
    }

    _handleBack() {
      this.context.router.goBack()
    }

    _handleTasks() {
      const { tasks } = this.page()
      this.context.tasks.open(tasks.items)
    }

    _handleTask() {
      const { task } = this.page()
      const { drawer, router, modal, tasks } = this.context
      if(task.route) {
        router.push(task.route)
      } else if(task.modal) {
        modal.open(task.modal)
      } else if(task.drawer){
        const location = task.location || 'right'
        drawer.open(task.drawer, location)
      } else if(task.handler){
        task.handler()
      }
      tasks.close()
    }

  }

  const mapStateToProps = (state, props) => ({
    unseen: state.maha.admin.unseen
  })

  return connect(mapStateToProps)(Page)

}
