import itemButtons from '../item_buttons'
import itemTasks from '../item_tasks'
import { Page } from 'maha-admin'
import Details from './details'
import React from 'react'
import Edit from './edit'

const mapResourcesToPage = (props, context) => ({
  settings: '/api/admin/expenses/settings',
  reimbursement: `/api/admin/expenses/reimbursements/${props.params.id}`
})

const mapPropsToPage = (props, context, resources, page) => ({
  title: 'Reimbursement',
  rights: ['expenses:manage_expenses'],
  panel: {
    component: <Details reimbursement={ resources.reimbursement } />
  },
  tasks: itemTasks('reimbursement', resources.reimbursement, context, Edit),
  buttons: itemButtons(resources.settings, 'reimbursement', resources.reimbursement, context)
})

export default Page(mapResourcesToPage, mapPropsToPage)
