import React from 'react'
import PropTypes from 'prop-types'
import Item from '../feed/item'

class Notifications extends React.Component {

  static childContextTypes = {
    notifications: PropTypes.object
  }

  static contextTypes = {
    admin: PropTypes.object,
    host: PropTypes.object,
    network: PropTypes.object,
    popup: PropTypes.object,
    router: PropTypes.object
  }

  static propTypes = {
    children: PropTypes.any,
    notifications: PropTypes.array,
    notificationIds: PropTypes.array,
    onAddNotification: PropTypes.func,
    onMarkVisited: PropTypes.func,
    onMarkSeen: PropTypes.func,
    onRemoveNotification: PropTypes.func
  }

  _handleNotification = this._handleNotification.bind(this)

  render() {
    return this.props.children
  }

  componentDidMount() {
    const { network } = this.context
    network.subscribe({ target: '/admin/notifications', action: 'add', handler: this._handleNotification })
  }

  componentDidUpdate(prevProps, prevState) {
    const { admin, popup } = this.context
    const { notifications } = this.props
    if(prevProps.notifications.length !== notifications.length) {
      if(notifications.length === 0) return popup.close()
      popup.open(() => (
        <div className="maha-feed-items">
          { notifications.map((item, index) => {
            return <Item key={`notification_${index}`} context="popup" item={ item } user={ admin.user } onClick={ this._handleClick.bind(this, item) } />
          })}
        </div>
      ))
    }
  }

  componentWillUnmount() {
    const { network } = this.context
    network.unsubscribe({ target: '/admin/notifications', action: 'add_notification', handler: this._handleNotification })
  }

  getChildContext() {
    const { onMarkSeen, onMarkVisited, onRemoveNotification } = this.props
    return {
      notifications: {
        markSeen: onMarkSeen,
        markVisited: onMarkVisited,
        removeNotification: onRemoveNotification
      }
    }
  }

  _handleNotification(notification) {
    const { onAddNotification, onMarkSeen, onRemoveNotification } = this.props
    const { host } = this.context
    onMarkSeen([notification.id])
    if(host.notifications) return this._handleHostNotification(notification)
    onAddNotification(notification)
    const audio = new Audio('/audio/notification.mp3')
    audio.play()
    setTimeout(() => onRemoveNotification(notification.id), 5000)
  }

  _handleHostNotification(notification) {
    const { host } = this.context
    host.pushNotification({
      title: 'New Notification',
      body: notification.description,
      url: `/admin/nv${notification.code}`
    })
  }

  _recordClick(notification) {
  }

  _handleClick(notification) {
    const { onRemoveNotification, onMarkVisited } = this.props
    const { router } = this.context
    if(!notification.is_visited) setTimeout(() => onMarkVisited(notification.id), 500)
    this._recordClick(notification)
    onRemoveNotification(notification.id)
    router.push(notification.url)
  }

}


export default Notifications
