export const INITIAL_STATE = {
  alerts: []
}

export default (state = INITIAL_STATE, action) => {

  switch (action.type) {

  case 'PUSH':
    return {
      ...state,
      alerts: [
        ...state.alerts,
        action.alert
      ]
    }

  case 'POP':
    return {
      ...state,
      alerts: state.alerts.slice(0,-1)
    }

  default:
    return state
  }

}
