import { Assignment, CompactUserToken } from 'maha-admin'
import PropTypes from 'prop-types'
import React from 'react'

class Users extends React.Component {

  static propTypes = {
    role: PropTypes.object,
    users: PropTypes.array
  }

  render() {
    return <Assignment { ...this._getAssignment() } />
  }

  _getAssignment() {
    const { role } = this.props
    return {
      action: `/api/admin/team/roles/${role.id}/users`,
      assignedEndpoint: `/api/admin/team/roles/${role.id}/users`,
      assignedFormat: CompactUserToken,
      empty: {
        icon: 'users',
        title: 'Add users',
        text: 'Please add users to this role'
      },
      label: 'User',
      text: 'full_name',
      title: 'Manage Users',
      unassignedEndpoint: '/api/admin/users',
      unassignedFormat: CompactUserToken,
      value: 'id'
    }
  }

}

export default Users
