import Notification from './components/notifications'
import initReactFastclick from 'react-fastclick'
import Root from './components/platform/root'
import Electron from './components/electron'
import Platform from './components/platform'
import Browser from './components/browser'
import Cordova from './components/cordova'
import Network from './components/network'
import Router from './components/router'
import Logger from './components/logger'
import Alerts from './components/alerts'
import NotFound from './views/not_found'
import Admin from './components/admin'
import { Flash, Popup } from 'reframe'
import PropTypes from 'prop-types'
import React from 'react'

initReactFastclick()

class Client extends React.Component {

  static childContextTypes = {
    platform: PropTypes.object
  }

  static contextTypes = {
    logger: PropTypes.object,
    router: PropTypes.object
  }

  static propTypes = {
    appBadges: PropTypes.array,
    appRoots: PropTypes.array,
    appReducers: PropTypes.array,
    appRoutes: PropTypes.array,
    appUserTasks: PropTypes.array,
    appUserFields: PropTypes.array,
    appUserValues: PropTypes.array
  }

  constructor(props) {
    super(props)
    this.routes = this._collapseRoutes({
      path: '',
      children: [
        ...this.props.appRoutes,
        { path: '/*', component: NotFound }
      ]
    })
  }

  render() {
    const { appBadges, appRoots, appReducers } = this.props
    const Host = this._getHost()
    return (
      <Root reducers={ appReducers }>
        <Logger>
          <Router routes={ this.routes }>
            <Alerts>
              <Network>
                <Flash>
                  <Popup>
                    <Host>
                      <Admin>
                        <Notification>
                          <Platform badges={ appBadges } routes={ this.routes } roots={ appRoots } />
                        </Notification>
                      </Admin>
                    </Host>
                  </Popup>
                </Flash>
              </Network>
            </Alerts>
          </Router>
        </Logger>
      </Root>
    )
  }

  componentDidCatch(error, info) {
    this.context.logger.error(error, info)
  }

  getChildContext() {
    const { appUserTasks, appUserFields, appUserValues } = this.props
    return {
      platform: {
        appUserTasks,
        appUserFields,
        appUserValues
      }
    }
  }

  _getHost() {
    if(navigator.userAgent.search('Cordova') >= 0) {
      return Cordova
    } else if(navigator.userAgent.search('Electron') >= 0) {
      return Electron
    } else {
      return Browser
    }
  }

  _collapseRoutes(routes, prefix = '') {
    return routes.children.reduce((routes, route) => {
      const path = (route.path !== '/') ? route.path : ''
      if(route.children) {
        return {
          ...routes,
          ...this._collapseRoutes(route, `${prefix}${path}`)
        }
      }
      return {
        ...routes,
        [`${prefix}${path}`]: route.component
      }
    }, {})
  }

}

export default Client
