import PropTypes from 'prop-types'
import React from 'react'
import Note from './note'
import Call from './call'

class Form extends React.Component {

  static propTypes = {
    active: PropTypes.number,
    onChoose: PropTypes.func
  }

  render() {
    const { active } = this.props
    const tabs = [
      { label: 'Note', component: <Note contact={{ id: 1 }} /> },
      { label: 'Call', component: <Call contact={{ id: 1 }} /> },
      { label: 'Email', component: <div>Email</div> },
      { label: 'Tasks', component: <div>Tasks</div> }
    ]
    return (
      <div className="crm-timeline-form">
        <div className="crm-timeline-form-tabs">
          { tabs.map((tab, index) => (
            <div className={ this._getTab(index) } key={ `tab_${index}` } onClick={ this._handleChoose.bind(this, index) }>
              { tab.label }
            </div>
          )) }
        </div>
        <div className="crm-timeline-form-body">
          { tabs[active].component }
        </div>
      </div>
    )
  }

  _getTab(index) {
    const { active } = this.props
    const classes = ['crm-timeline-form-tab']
    if(index === active) classes.push('active')
    return classes.join(' ')
  }

  _handleChoose(index) {
    this.props.onChoose(index)
  }


}

export default Form
