import { createSelector } from 'reselect'
import _ from 'lodash'

const assignedSelector = (state, props) => state.assigned

const valueSelector = (state, props) => props.type === 'user' ? 'user.id' : 'project.id'

export const ids = createSelector(
  assignedSelector,
  valueSelector,
  (assigned, value) => assigned.map(membership => _.get(membership, value))
)

export const values = createSelector(
  assignedSelector,
  valueSelector,
  (assigned, value) => assigned.reduce((values, membership) => [
    ...values,
    { [value.replace('.', '_')]: _.get(membership, value), member_type_id: membership.member_type.id }
  ], [])
)
