import PropTypes from 'prop-types'
import React from 'react'
import _ from 'lodash'

class Complete extends React.Component {

  static contextTypes = {
    admin: PropTypes.object,
    flash: PropTypes.object,
    router: PropTypes.object
  }

  static propTypes = {
    team: PropTypes.object,
    token: PropTypes.string,
    user: PropTypes.object,
    onChangeMode: PropTypes.func
  }

  _handleClick = this._handleClick.bind(this)

  render() {
    return (
      <div className="maha-signin-panel">
        <div className="maha-signin-form">
          <div className="maha-signin-content">
            <h1><i className="fa fa-check-circle" /></h1>
            <h2>Congratulations!</h2>
            <p>Your password was successfully reset!</p>
            <div className="field button-field">
              <button className="ui fluid large button" onClick={ this._handleClick }>
                Continue <i className="right chevron icon" />
              </button>
            </div>
          </div>
        </div>
      </div>
    )
  }

  _handleClick() {
    const { team, token, user } = this.props
    const { admin, flash, router } = this.context
    const index = _.findIndex(admin.teams, { id: team.id, user: { id: user.id } })
    if(index >= 0) {
      admin.signin(index, token)
    } else {
      admin.addTeam(team, token, user)
    }
    setTimeout(() => {
      flash.set('success', 'Your password has been successfully reset')
    }, 500)
    router.push('/admin')
  }

}

export default Complete
