import { Routes } from 'maha-admin'
import ContactsList from './views/contacts/list'
import ContactsShow from './views/contacts/show'
import OrganizationsList from './views/organizations/list'
import OrganizationsShow from './views/organizations/show'
import DealsList from './views/deals/list'
import DealsShow from './views/deals/show'
import FieldsList from './views/fields/list'
import FieldsShow from './views/fields/show'

const routes = new Routes([
  { path: '/:type/fields', component: FieldsList },
  { path: '/:type/fields/:id', component: FieldsShow },
  { path: '/contacts', component: ContactsList },
  { path: '/contacts/:id', component: ContactsShow },
  { path: '/organizations', component: OrganizationsList },
  { path: '/organizations/:id', component: OrganizationsShow },
  { path: '/deals', component: DealsList },
  { path: '/deals/:id', component: DealsShow }
])

export default routes
