import PropTypes from 'prop-types'
import React from 'react'
import _ from 'lodash'
import Emojis from '../emojis'

class Richtextfield extends React.Component {

  state = {
    q: '',
    height: 32,
    emojis: false
  }

  static propTypes = {
    autofocus: PropTypes.bool,
    defaultValue: PropTypes.string,
    placeholder: PropTypes.string,
    onChange: PropTypes.func,
    onEsc: PropTypes.func,
    onEnter: PropTypes.func,
    onType: PropTypes.func
  }

  static defaultProps = {
    autofocus: false,
    onChange: (q) => {},
    onEsc: (q) => {},
    onEnter: (q) => {},
    onType: (q) => {}
  }

  _handleEsc = this._handleEsc.bind(this)
  _handleEnter = this._handleEnter.bind(this)
  _handleToggleEmojis = this._handleToggleEmojis.bind(this)
  _handleType = this._handleType.bind(this)
  _handleUpdate = this._handleUpdate.bind(this)

  render() {
    const { emojis } = this.state
    return (
      <div className="maha-richtextfield">
        <div className="maha-richtextfield-input">
          <textarea placeholder={ this.props.placeholder } { ...this._getInput() } />
        </div>
        <div className="maha-richtextfield-tools">
          <i className="fa fa-smile-o" onClick={ this._handleToggleEmojis } />
          <i className="fa fa-camera" />
        </div>
        { emojis && <Emojis { ...this._getEmojis() } /> }
      </div>
    )

  }

  componentDidMount() {
    const { autofocus, defaultValue } = this.props
    if(defaultValue) this.setState({ q: defaultValue })
    if(autofocus) this.textarea.focus()
  }

  componentDidUpdate(prevProps, prevState) {
    const { defaultValue } = this.props
    const { q } = this.state
    if(q !== prevState.q) {
      const height = this.textarea.scrollHeight
      if(height !== this.state.height ) this.setState({ height })
    }
    if(defaultValue !== prevProps.defaultValue) {
      this.setState({ q: defaultValue })
    }
  }

  _getEmojis() {
    return {
      onChange: this._handleUpdate
    }
  }

  _handleUpdate(emoji) {
    const q = this.state.q + emoji
    this.setState({ q })
  }

  _getInput() {
    const { q, height } = this.state
    return {
      ref: node => this.textarea = node,
      onKeyDown: this._handleEsc,
      onKeyPress: this._handleEnter,
      onChange: this._handleType,
      value: q,
      style: { height }
    }
  }

  _handleToggleEmojis() {
    this.setState({ emojis: !this.state.emojis })
  }

  _handleEsc(e) {
    if(e.keyCode === 27) {
      this.props.onEsc()
      e.preventDefault()
      return false
    }
  }

  _handleEnter(e) {
    if(e.key === 'Enter' && !e.shiftKey && !_.isEmpty(e.target.value)) {
      this.props.onEnter(e.target.value)
      e.preventDefault()
      return false
    }
  }

  _handleType(e) {
    const q = e.target.value
    this.setState({ q })
    this.props.onChange(q)
  }

  _handleReset() {
    this.setState({
      q: '',
      height: 32
    })
  }

  _handleClear() {
    this.setState({
      q: ''
    })
  }

}

export default Richtextfield
