import { CSSTransition } from 'react-transition-group'
import Notifications from './notifications'
import PropTypes from 'prop-types'
import Question from './question'
import Password from './password'
import Complete from './complete'
import Invalid from './invalid'
import Welcome from './welcome'
import Verify from './verify'
import Avatar from './avatar'
import Answer from './answer'
import React from 'react'
import _ from 'lodash'

class Activate extends React.Component {

  static contextTypes = {
    admin: PropTypes.object,
    flash: PropTypes.object,
    router: PropTypes.object
  }

  static propTypes = {
    error: PropTypes.string,
    location: PropTypes.string,
    mode: PropTypes.string,
    status: PropTypes.string,
    onVerify: PropTypes.func
  }

  constructor(props) {
    super(props)
    this.classNames = ''
    this.routes = [
      { mode: 'verify', component: Verify },
      { mode: 'invalid', component: Invalid },
      { mode: 'welcome', component: Welcome },
      { mode: 'question', component: Question },
      { mode: 'answer', component: Answer },
      { mode: 'password', component: Password },
      { mode: 'avatar', component: Avatar },
      { mode: 'notifications', component: Notifications },
      { mode: 'complete', component: Complete }
    ]
    this.state = {
      transition: 'next'
    }
  }

  render() {
    const { mode } = this.props
    const { transition } = this.state
    return (
      <div className={`maha-signin ${transition}`}>
        { this.routes.map((route, index) => (
          <CSSTransition in={ mode === route.mode } classNames="advance" timeout={ 5000 } mountOnEnter={ true } unmountOnExit={ true } key={ index }>
            <route.component { ...this.props } />
          </CSSTransition>
        ))}
      </div>
    )
  }

  componentDidMount() {
    const { location, onVerify } = this.props
    const [,token] = location.pathname.match(/^\/admin\/activate\/(.*)/)
    onVerify(token)
  }

  componentWillUpdate(nextProps) {
    const { mode } = this.props
    if(nextProps.mode !== mode) {
      const oldIndex = _.findIndex(this.routes, { mode })
      const newIndex = _.findIndex(this.routes, { mode: nextProps.mode })
      if(oldIndex >= 0) {
        const transition = (newIndex >= oldIndex) ? 'slide-next' : 'slide-back'
        this.setState({ transition })
      }
    }
  }

  componentDidUpdate(prevProps) {
    const { flash, router } = this.context
    const { error, mode, status } = this.props
    if(prevProps.status !== status && status === 'failure') {
      flash.set('error', error)
    }
    if(mode !== prevProps.mode) {
      const pathname = `/admin/activate/${mode}`
      router.push(pathname)
    }

  }

}

export default Activate
