export const fetchMemberTypes = () => ({
  type: 'API_REQUEST',
  method: 'GET',
  endpoint: '/api/admin/expenses/member_types',
  request: 'FETCH_MEMBER_TYPES_REQUEST',
  success: 'FETCH_MEMBER_TYPES_SUCCESS',
  failure: 'FETCH_MEMBER_TYPES_FAILURE'
})

export const fetchAssigned = (endpoint) => ({
  type: 'API_REQUEST',
  method: 'GET',
  endpoint,
  request: 'FETCH_ASSIGNED_REQUEST',
  success: 'FETCH_ASSIGNED_SUCCESS',
  failure: 'FETCH_ASSIGNED_FAILURE'
})

export const save = (endpoint, members) => ({
  type: 'API_REQUEST',
  method: 'PATCH',
  body: { members },
  endpoint,
  request: 'SAVE_REQUEST',
  success: 'SAVE_SUCCESS',
  failure: 'SAVE_FAILURE'
})

export const add = (model, item) => ({
  type: 'ADD',
  model,
  item
})

export const remove = (index) => ({
  type: 'REMOVE',
  index
})

export const changeType = (index) => ({
  type: 'CHANGE_TYPE',
  index
})

export const beginAdd = () => ({
  type: 'BEGIN_ADD'
})
