import React from 'react'
import PropTypes from 'prop-types'
import Avatar from '../avatar'
import _ from 'lodash'

class Password extends React.Component {

  static contextTypes = {
    admin: PropTypes.object,
    flash: PropTypes.object,
    router: PropTypes.object
  }

  static propTypes = {
    show: PropTypes.bool,
    status: PropTypes.string,
    team: PropTypes.object,
    token: PropTypes.string,
    user: PropTypes.object,
    onSet: PropTypes.func,
    onPassword: PropTypes.func,
    onForgot: PropTypes.func,
    onTogglePassword: PropTypes.func
  }

  password = null

  _handleBack = this._handleBack.bind(this)
  _handleForgot = this._handleForgot.bind(this)
  _handleSubmit = this._handleSubmit.bind(this)
  _handleTogglePassword = this._handleTogglePassword.bind(this)

  render() {
    const { show, status, user } = this.props
    return (
      <div className="maha-signin-panel">
        <div className="maha-signin-form">
          <div className="maha-signin-content">
            { user && <Avatar user={ user } width="150" /> }
            { user && <h2>{ user.full_name }</h2> }
            { user && <p>{ user.email }</p> }
          </div>
          <form className="ui form" onSubmit={ this._handleSubmit }>
            <div className="field password-field">
              <div className="ui fluid left icon input">
                <i className="lock icon"></i>
                <input className="form-control" autoComplete="off" autoCapitalize="off" autoCorrect="off" spellCheck="false" placeholder="Password" type={ show ? 'text' : 'password' } ref={ (node) => this.password = node } />
                <a onClick={ this._handleTogglePassword }>{ show ? 'HIDE' : 'SHOW' }</a>
              </div>
            </div>
            <div className="field button-field">
              <button className={`ui fluid large ${(status === 'submitting') ? 'loading' : ''} button`}>Signin</button>
            </div>
          </form>
          <div className="maha-signin-content">
            <p><a onClick={ this._handleBack }>Not you?</a></p>
            <p><a onClick={ this._handleForgot }>Forgot your password?</a></p>
          </div>
        </div>
      </div>
    )
  }

  componentDidMount() {
    this.props.onSet(this.props.team, this.props.user, 'password')
  }

  componentDidUpdate(prevProps) {
    const { status, team, token, user } = this.props
    const { admin, flash, router } = this.context
    if(prevProps.status !== status && status === 'reset') {
      flash.set('info', 'Please check your email for password reset instructions')
    }
    if(prevProps.token !== token) {
      this.password.blur()
      const index = _.findIndex(admin.teams, { id: team.id, user: { id: user.id } })
      team.strategy = 'local'
      if(index >= 0) {
        if(admin.teams[index].token) {
          admin.chooseTeam(index)
        } else {
          admin.signin(index, token)
        }
      } else {
        admin.addTeam(team, token, user)
      }
      router.push('/admin')
    }
  }

  _handleBack() {
    this.context.router.goBack()
  }

  _handleSubmit(e) {
    const { team, user, onPassword } = this.props
    const password = this.password.value
    onPassword(team.id, user.email, password)
    e.preventDefault()
    return false
  }

  _handleTogglePassword() {
    this.props.onTogglePassword()
  }

  _handleForgot() {
    const { team, user, onForgot } = this.props
    onForgot(team.id, user.email)
  }

}

export default Password
