import AssetViewer from '../asset/viewer'
import AssetToken from '../asset/token'
import Attachment from '../attachment'
import PropTypes from 'prop-types'
import Image from '../image'
import React from 'react'
import emojify from '../emojis/emojify'

const EMAIL_REGEX = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/


class RichText extends React.Component {

  static contextTypes = {
    router: PropTypes.object
  }

  static propTypes = {
    attachments: PropTypes.array,
    text: PropTypes.string
  }

  render() {
    const { text } = this.props
    const sorted = this.props.attachments.reduce((sorted, attachment) => {
      const type = this._getType(attachment)
      return {
        ...sorted,
        [type]: [
          ...sorted[type],
          attachment
        ]
      }
    }, { files: [], images: [], media: [], links: [] })
    const attachments = this.props.attachments.length > 0 ? (
      <div className="maha-attachment" key="attachment">
        { sorted.images.length > 0 &&
          <div className={`maha-gallery maha-gallery-${sorted.images.length}`}>
            { sorted.images.map((image, index) => (
              <div className="maha-gallery-photo" key={`gallery_photo_${image.id}`} onClick={ this._handleClick.bind(this, image.asset) }>
                <Image src={ image.asset.path } transforms={{ fit: 'cover', w: 300, h: 300 }} />
                <img src={ `/admin/images/${image.asset.source}.png` } className="maha-gallery-photo-source" />
              </div>
            ))}
          </div>
        }
        { sorted.media.length > 0 &&
          <div className="maha-medias">
            { sorted.media.map((video, index) => (
              <div className="maha-media" key={`video_${index}`} onClick={ this._handleClick.bind(this, video.asset) }>
                <AssetViewer asset={ video.asset } />
                <AssetToken { ...video.asset } />
              </div>
            ))}
          </div>
        }
        { sorted.files.length > 0 &&
          <div className="maha-files">
            { sorted.files.map((file, index) => (
              <div className="maha-files-file" key={`gallery_photo_${file.id}`}>
                <Attachment { ...file } key={`files_file_${index}`} />
              </div>
            ))}
          </div>
        }
        { sorted.links.length > 0 &&
          <div className="maha-links">
            { sorted.links.map((link, index) => (
              <div className="maha-links-link" key={`link-${link.id}`}>
                <Attachment { ...link } key={`links_link_${index}`} />
              </div>
            ))}
          </div>
        }
      </div>
    ) : null
    const transformed = this.props.attachments.reduce((text, attachment, index) => {
      if(text.indexOf(attachment.from_url) < 0) return text
      return text.replace(attachment.from_url, '')
    }, text).split('<|>').map((segment, index) => {
      const attachment = segment.match(/\[attachment(\d*)\]/)
      if(attachment) return null
      const transforms = [
        { regex: /```([^`]*)```/mg, replacement: '<div class="maha-richtext-code"><code>$1</code></div>' },
        { regex: /`([^`]*)`/g, replacement: '<span class="maha-richtext-snippet">$1</span>' },
        { regex: /\n/g, replacement: '<br />' },
        { regex: EMAIL_REGEX, replacement: '<a class="maha-richtext-email" href="mailto:$&">$&</a>' }
      ]
      const with_emojis = emojify(segment)
      const __html = transforms.reduce((html, transform) => {
        return html.replace(transform.regex, transform.replacement)
      }, with_emojis)
      if(__html.length === 0 || __html === ' ') return null
      return <div className="maha-richtext-text" key={`line_${index}`} dangerouslySetInnerHTML={{ __html }} />
    })
    return [
      attachments,
      transformed
    ]
  }

  _getType(attachment) {
    if(attachment.type === 'local') return 'links'
    if(!attachment.asset) return 'files'
    if(attachment.asset.content_type.match(/image/)) return 'images'
    if(attachment.asset.content_type.match(/video/)) return 'media'
    if(attachment.asset.content_type.match(/audio/)) return 'media'
    return 'files'
  }

  _handleClick(asset) {
    const { router } = this.context
    router.push(`/admin/assets/${asset.id}`)
  }


}

export default RichText
