import React from 'react'
import PropTypes from 'prop-types'
import { CSSTransition } from 'react-transition-group'
import Accounts from './accounts'
import Team from './team'
import Email from './email'
import Password from './password'
import _ from 'lodash'

class Signin extends React.Component {

  static contextTypes = {
    admin: PropTypes.object,
    flash: PropTypes.object,
    router: PropTypes.object
  }

  static propTypes = {
    error: PropTypes.string,
    location: PropTypes.object,
    mode: PropTypes.string,
    status: PropTypes.string,
    team: PropTypes.object,
    user: PropTypes.object,
    onChangeMode: PropTypes.func
  }

  constructor(props) {
    super(props)
    this.classNames = ''
    this.routes = [
      { pathname: '/admin/signin/accounts', component: Accounts },
      { pathname: '/admin/signin/team', component: Team },
      { pathname: '/admin/signin/email', component: Email },
      { pathname: '/admin/signin/password', component: Password }
    ]
  }

  render() {
    const { location } = this.props
    return (
      <div className="maha-signin">
        { this.routes.map(route => {
          return (
            <CSSTransition in={ route.pathname === location.pathname } classNames={ this.classNames } timeout={ 500 } mountOnEnter={ true } unmountOnExit={ true } key={route.pathname}>
              <route.component { ...this.props } />
            </CSSTransition>
          )
        })}
      </div>
    )
  }

  componentDidMount() {
    const { teams } = this.context.admin
    const { onChangeMode } = this.props
    const inactive = teams.filter(team => _.isNil(team.token))
    const mode = (inactive.length === 0) ? 'team' : 'accounts'
    onChangeMode(mode)
  }

  componentWillUpdate(nextProps) {
    const { location } = this.props
    if(nextProps.location.pathname !== location.pathname) {
      const oldIndex = _.findIndex(this.routes, { pathname: location.pathname })
      const newIndex = _.findIndex(this.routes, { pathname: nextProps.location.pathname })
      if(oldIndex >= 0) {
        this.classNames = (newIndex > oldIndex) ? 'slide-next' : 'slide-back'
      }
    }
  }

  componentDidUpdate(prevProps) {
    const { flash, router } = this.context
    const { error, mode, team, user, status, onChangeMode } = this.props
    if(prevProps.status !== status && status === 'failure') {
      flash.set('error', error)
    }
    if(!_.isEqual(prevProps.team, team)) {
      if(team.strategy === 'local' || (!team.strategy && _.includes(team.strategies, 'local'))) {
        if(user) {
          onChangeMode('password')
        } else {
          onChangeMode('email')
        }
      } else if(team.strategy) {
        onChangeMode(team.strategy)
      } else {
        onChangeMode(team.strategies[0])
      }
    }
    if(mode !== prevProps.mode && mode) {
      const pathname = `/admin/signin/${mode}`
      if(_.findIndex(this.routes, { pathname }) < 0) {
        const state = btoa(JSON.stringify({ team_id: team.id }))
        window.location = `${pathname}?state=${state}`
      } else if(!prevProps.mode) {
        router.replace(pathname)
      } else {
        router.push(pathname)
      }
    }
  }

}

export default Signin
