import { Assignment2, CompactUserToken } from 'maha-admin'
import PropTypes from 'prop-types'
import React from 'react'

class New extends React.Component {

  static contextTypes = {
    modal: PropTypes.object
  }

  render() {
    return <Assignment2 { ...this._getAssignment() } />
  }

  _getAssignment() {
    return {
      action: '/api/admin/team/supervisors',
      method: 'POST',
      endpoint: '/api/admin/team/supervisors/all',
      multiple: false,
      name: 'user_id',
      value: 'id',
      text: 'full_name',
      format: CompactUserToken,
      filters: [
        { label: 'Group', name: 'group_id', type: 'select', multiple: true, endpoint: '/api/admin/team/groups', value: 'id', text: 'title', sort: { key: 'title', order: 'asc' } }
      ],
      title: 'Choose Supervisor'
    }
  }

}

export default New
