import { Page } from 'maha-admin'

const mapPropsToPage = (props, context, resources, page) => ({
  title: 'Queues',
  collection: {
    table: [
      { label: 'Name', key: 'name', primary: true },
      { label: 'Total', key: 'total', primary: false },
      { label: 'Active', key: 'active', primary: false },
      { label: 'Wait', key: 'wait', primary: false },
      { label: 'Failed', key: 'failed', primary: false },
      { label: 'Completed', key: 'completed', primary: false },
      { label: 'Delayed', key: 'delayed', primary: false }
    ],
    endpoint: '/api/admin/platform/queues',
    entity: 'queue',
    link: (record) => `/admin/platform/queues/${record.id}`,
    recordTasks: [
      {
        label: 'Empty Queue',
        request: (id) => ({
          method: 'PATCH',
          endpoint: `/api/admin/platform/queues/${id}/empty`,
          onFailure: (result) => context.flash.set('error', 'Unable to empty this queue'),
          onSuccess: (result) => context.flash.set('success', 'The queue has been emptied')
        })
      }
    ]
  }
})

export default Page(null, mapPropsToPage)
