import CompactReviewToken from '../review_token/compact'
import RichTextField from '../richtextfield'
import ReviewToken from '../review_token'
import PropTypes from 'prop-types'
import pluralize from 'pluralize'
import Avatar from '../avatar'
import Rating from './rating'
import moment from 'moment'
import React from 'react'
import _ from 'lodash'

class Reviews extends React.Component {

  static contextTypes = {
    admin: PropTypes.object,
    network: PropTypes.object
  }

  static propTypes = {
    action: PropTypes.string,
    editing: PropTypes.string,
    endpoint: PropTypes.string,
    entity: PropTypes.string,
    hidden: PropTypes.number,
    placeholder: PropTypes.string,
    q: PropTypes.string,
    reviews: PropTypes.array,
    score: PropTypes.number,
    status: PropTypes.string,
    typing: PropTypes.object,
    onAdd: PropTypes.func,
    onBeginEdit: PropTypes.func,
    onCancelEdit: PropTypes.func,
    onCreate: PropTypes.func,
    onDestroy: PropTypes.func,
    onRemove: PropTypes.func,
    onLoad: PropTypes.func,
    onRate: PropTypes.func,
    onReplace: PropTypes.func,
    onSetTyping: PropTypes.func,
    onShowMore: PropTypes.func,
    onType: PropTypes.func,
    onUpdate: PropTypes.func
  }

  static defaultProps = {
    placeholder: 'Write a review...'
  }

  _handleCancelEdit = this._handleCancelEdit.bind(this)
  _handleCreate = this._handleCreate.bind(this)
  _handleNotification = this._handleNotification.bind(this)
  _handleRate = this._handleRate.bind(this)
  _handleShowMore = this._handleShowMore.bind(this)
  _handleType = this._handleType.bind(this)
  _handleUpdate = this._handleUpdate.bind(this)

  render() {
    const { reviews, editing, hidden, status, typing } = this.props
    const { admin } = this.context
    const sort = (a,b) => {
      if(a.created_at > b.created_at) return 1
      if(a.created_at < b.created_at) return -1
      return 0
    }
    if(status === 'loading') {
      return (
        <div className="maha-comments message">
          <i className="fa fa-circle-o-notch fa-spin" /><br />
          Loading reviews...
        </div>
      )
    }
    if(status === 'failed') {
      return (
        <div className="maha-comments message">
          <i className="fa fa-warning" /><br />
          Unable to load reviews
        </div>
      )
    }
    const review_count = reviews.length
    const review_total = reviews.reduce((total, review) => (total + review.score), 0)
    const review_average = review_count > 0 ? review_total / review_count : 0
    return (
      <div className="maha-comments">
        <div className="maha-comments-summary">
          <ReviewToken score={ review_average } count={ review_count } />
        </div>
        { hidden > 0 &&
          <div className="maha-comments-more" onClick={ this._handleShowMore }>
            View { hidden } more { pluralize('review', hidden) }
          </div>
        }
        { reviews.sort(sort).map((review, index) => {
          if(index < hidden) return
          const score = Math.min(Math.max(review.score, 0), 5)
          return (
            <div className="maha-comment" key={`review_${index}`}>
              <div className="maha-comment-user">
                <Avatar user={ review.user } />
              </div>
              <div className="maha-comment-bubble">
                { review.uid === editing ?
                  <div className="maha-comment-form">
                    <Rating { ...this._getEditRating(review) } />
                    <div className="maha-comment-field">
                      <RichTextField { ...this._getEditTextarea(review) } />
                    </div>
                  </div> :
                  <div className="maha-comment-text">
                    <div className="maha-comment-header">
                      <CompactReviewToken score={ score } />
                      <div className="maha-comment-author">{ review.user.full_name }</div>
                      <div className="maha-comment-timestamp">{ moment(review.created_at).format('MMM D @ hh:mm A') }</div>
                    </div>
                    <div className="maha-comment-body">
                      <span dangerouslySetInnerHTML={{ __html: review.text.replace(/\n/g, '<br />') }} />
                    </div>
                  </div>
                }
              </div>
              { !editing && review.user.id === admin.user.id &&
                <div className="maha-comment-actions">
                  <div className="maha-comment-action" onClick={ this._handleEdit.bind(this, review) }>
                    <i className="fa fa-fw fa-pencil" />
                  </div>
                  <div className="maha-comment-action" onClick={ this._handleRemove.bind(this, review) }>
                    <i className="fa fa-fw fa-trash" />
                  </div>
                </div>
              }

            </div>
          )
        }) }
        { typing &&
          <div className="maha-comment">
            <div className="maha-comment-user">
              <Avatar user={ typing } />
            </div>
            <div className="maha-comment-bubble">
              <div className="maha-comment-typing">
                { typing.full_name } is typing
              </div>
            </div>
          </div>
        }
        { !editing &&
          <div className="maha-comment">
            <div className="maha-comment-user">
              <Avatar user={ admin.user } />
            </div>
            <div className="maha-comment-bubble">
              <div className="maha-comment-form">
                <Rating { ...this._getCreateRating() } />
                <div className="maha-comment-field">
                  <RichTextField { ...this._getCreateTextarea() } />
                </div>
              </div>
            </div>
          </div>
        }
      </div>
    )
  }

  componentDidMount() {
    const { entity, onLoad } = this.props
    const { network } = this.context
    onLoad(`/api/admin/${entity}/reviews`)
    network.join(`/admin/${entity}/reviews`)
    network.subscribe([
      { target: `/admin/${entity}/reviews`, action: 'refresh', handler: this._handleNotification }
    ])
  }

  componentDidUpdate(prevProps) {
    const { admin, network } = this.context
    const { entity, q } = this.props
    const channel = `/admin/${entity}/reviews`
    if(q.length > 0 && prevProps.q.length === 0) {
      network.message(channel, admin.team.token, { action: 'beginType', user: admin.user })
    }
    if(q.length === 0 && prevProps.q.length > 0) {
      network.message(channel, admin.team.token, { action: 'endType', user: admin.user })
    }
  }

  componentWillUnmount() {
    const { entity } = this.props
    const { network } = this.context
    network.leave(`/admin/${entity}/reviews`)
    network.unsubscribe([
      { target: `/admin/${entity}/reviews`, action: 'refresh', handler: this._handleNotification }
    ])
  }

  _getCreateRating() {
    return {
      defaultValue: this.props.score,
      onChange: this._handleRate
    }
  }

  _getEditRating(review) {
    return {
      defaultValue: this.props.score,
      onChange: this._handleRate
    }
  }

  _getCreateTextarea() {
    return {
      defaultValue: this.props.q,
      placeholder: this.props.placeholder,
      onEnter: this._handleCreate,
      onChange: this._handleType
    }
  }

  _getEditTextarea(review) {
    return {
      autofocus: true,
      defaultValue: this.props.q,
      placeholder: this.props.placeholder,
      onEsc: this._handleCancelEdit,
      onEnter: this._handleUpdate,
      onChange: this._handleType
    }
  }

  _handleShowMore() {
    this.props.onShowMore()
  }

  _handleClick() {
    const { editing } = this.props
    if(editing) return this._handleUpdate(this.props.q)
    this._handleCreate(this.props.q)
  }

  _handleType(text) {
    this.props.onType(text)
  }

  _handleRate(score) {
    this.props.onRate(score)
  }

  _handleCreate(text) {
    const { admin } = this.context
    const { entity, score, onCreate } = this.props
    if(_.isEmpty(text) || score === 0) return
    const review = {
      user: admin.user,
      uid: _.random(100000000, 999999999).toString(36),
      score,
      text,
      created_at: moment().utc().format('YYYY-MM-DDTHH:mm:ss.SSS') + 'Z',
      updated_at: moment().utc().format('YYYY-MM-DDTHH:mm:ss.SSS') + 'Z'
    }
    onCreate(`/api/admin/${entity}/reviews`, review)
  }

  _handleUpdate(text) {
    const { entity, editing, reviews, score, onUpdate } = this.props
    if(_.isEmpty(text) || score === 0) return
    const review = {
      ..._.find(reviews, { uid: editing }),
      score,
      text,
      updated_at: moment().utc().format('YYYY-MM-DDTHH:mm:ss.SSS') + 'Z'
    }
    onUpdate(`/api/admin/${entity}/reviews/${editing}`, review)
  }

  _handleNotification(notification) {
    if(notification.action === 'add') this._handleAddNotifcation(notification.review)
    if(notification.action === 'remove') this._handleRemoveNotifcation(notification.uid)
    if(notification.action === 'replace') this._handleReplaceNotifcation(notification.review)
    if(notification.action === 'beginType') this._handleBeginType(notification.user)
    if(notification.action === 'endType') this._handleEndType(notification.user)
  }

  _handleAddNotifcation(review) {
    const { onAdd } = this.props
    onAdd(review)
  }

  _handleRemoveNotifcation(uid) {
    const { onRemove } = this.props
    onRemove(uid)
  }

  _handleReplaceNotifcation(review) {
    const { onReplace } = this.props
    onReplace(review)
  }

  _handleBeginType(user) {
    const { onSetTyping } = this.props
    const { admin } = this.context
    if(admin.user.id !== user.id) onSetTyping(user)
  }

  _handleEndType(user) {
    this.props.onSetTyping(null)
  }

  _handleEdit(review) {
    const { onBeginEdit } = this.props
    onBeginEdit(review.text, review.uid)
  }

  _handleCancelEdit() {
    const { onCancelEdit } = this.props
    onCancelEdit()
  }

  _handleRemove(review) {
    const { entity, onDestroy } = this.props
    onDestroy(`/api/admin/${entity}/reviews/${review.uid}`, review)
  }

}

export default Reviews
