import React from 'react'
import PropTypes from 'prop-types'
import { Page } from 'maha-admin'
import EmailTemplatesEdit from './edit'
import _ from 'lodash'

class EmailTemplatesShow extends React.Component {

  static propTypes = {
    email_template: PropTypes.object
  }

  render() {
    const { email_template } = this.props
    const layout = _.template(email_template.layout, { interpolate: /<%=([\s\S]+?)%>/g })
    const __html = layout({ content: email_template.html })
    return (
      <div className="email-canvas">
        <div className="email">
          <div className="email-header">
            <strong>Name:</strong> { email_template.name }<br />
            <strong>Subject:</strong> { email_template.subject }
          </div>
          <div className="email-body" dangerouslySetInnerHTML={{ __html }} />
        </div>
      </div>
    )
  }

}

const mapResourcesToPage = (props, context) => ({
  email_template: `/api/admin/team/email_templates/${props.params.id}`
})

const mapPropsToPage = (props, context, resources, page) => ({
  title: resources.email_template.name,
  component: EmailTemplatesShow,
  rights: ['team:manage_team'],
  tasks: {
    items: [
      {
        label: 'Edit Email Template',
        modal: (props) => <EmailTemplatesEdit emailTemplate={ resources.email_template }/>,
        rights: ['team:manage_team']
      }
    ]
  }

})

export default Page(mapResourcesToPage, mapPropsToPage)
