import matchPath from 'react-router-dom/matchPath'
import { BrowserRouter } from 'react-router-dom'
import PropTypes from 'prop-types'
import React from 'react'

class Router extends React.Component {

  static childContextTypes = {
    router: PropTypes.object
  }

  static contextTypes = {
    router: PropTypes.object
  }

  static propTypes = {
    children: PropTypes.any,
    routes: PropTypes.object
  }

  _handleBack = this._handleBack.bind(this)
  _handlePush = this._handlePush.bind(this)
  _handleReplace = this._handleReplace.bind(this)

  render() {
    return this.props.children
  }

  getChildContext() {
    const { router } = this.context
    return {
      router: {
        action: router.history.action,
        pathname: router.history.location.pathname,
        search: router.history.location.search,
        hash: router.history.location.hash,
        goBack: this._handleBack,
        push: this._handlePush,
        replace: this._handleReplace
      }
    }
  }

  _handleGetParams(pathname) {
    return Object.keys(this.props.routes).reduce((params, path) => {
      if(params) return params
      const matched = matchPath(pathname, { path, exact: true })
      return matched ? matched.params : null
    }, null)
  }

  _handleBack() {
    this.context.router.history.goBack()
  }

  _handleReplace(path) {
    this.context.router.history.replace(path)
  }

  _handlePush(path) {
    this.context.router.history.push(path)
  }

}

class RouterWrapper extends React.Component {

  static propTypes = {
    children: PropTypes.any
  }

  render() {
    return (
      <BrowserRouter>
        <Router { ...this.props }>
          { this.props.children }
        </Router>
      </BrowserRouter>
    )
  }

}

export default RouterWrapper
