import PropTypes from 'prop-types'
import { Form } from 'maha-admin'
import field from '../field'
import React from 'react'

class Edit extends React.Component {

  static contextTypes = {
    admin: PropTypes.object,
    modal: PropTypes.object
  }

  static propTypes = {
    fields: PropTypes.array,
    organization: PropTypes.object
  }

  _handleCancel = this._handleCancel.bind(this)
  _handleSuccess = this._handleSuccess.bind(this)

  render() {
    return <Form { ...this._getForm() } />
  }

  _getForm() {
    const { fields, organization } = this.props
    const { token } = this.context.admin.team
    return {
      title: 'Edit User',
      method: 'patch',
      endpoint: `/api/admin/crm/organizations/${organization.id}/edit`,
      action: `/api/admin/crm/organizations/${organization.id}`,
      onCancel: this._handleCancel,
      onSuccess: this._handleSuccess,
      sections: [
        {
          fields: [
            { label: 'Name', name: 'name', type: 'textfield', required: true },
            { label: 'Logo', name: 'logo_id', type: 'filefield', prompt: 'Choose Logo', action: '/api/admin/assets/upload', endpoint: '/api/admin/assets', token, multiple: false },
            ...fields.map(field)
          ]
        }
      ]
    }
  }

  _handleCancel() {
    this.context.modal.close()
  }

  _handleSuccess() {
    this.context.modal.close()
  }

}

export default Edit
