import PropTypes from 'prop-types'
import React from 'react'
import _ from 'lodash'

class Subscriptions extends React.Component {

  static propTypes = {
    defaultValue: PropTypes.array,
    ignored: PropTypes.array,
    notifications: PropTypes.array,
    status: PropTypes.string,
    onChange: PropTypes.func,
    onLoad: PropTypes.func,
    onReady: PropTypes.func,
    onSet: PropTypes.func,
    onToggle: PropTypes.func
  }

  render() {
    const { ignored, notifications } = this.props
    return (
      <div className="maha-subscription">
        { notifications.map((app, index) => [
          <div className="maha-subscription-app" key={`app_${index}`}>
            <div className={`maha-subscription-app-icon ${app.color}`}>
              <i className={`fa fa-fw fa-${app.icon}`} />
            </div>
            { app.title }
          </div>,
          app.notifications.map((notification, i) => (
            <div className="maha-subscription-notification" key={`notification_${i}`} onClick={ this._handleToggle.bind(this, notification.id) }>
              <div className="maha-subscription-notification-icon">
                { !_.includes(ignored, notification.id) ? <i className="fa fa-fw fa-check-circle" /> : <i className="fa fa-fw fa-circle-o" />}
              </div>
              <div className="maha-subscription-notification-label">
                { notification.description }
              </div>
            </div>
          ))
        ])}
      </div>
    )
  }

  componentDidMount() {
    const { defaultValue, onSet, onLoad } = this.props
    if(defaultValue) onSet(defaultValue)
    onLoad()
  }

  componentDidUpdate(prevProps) {
    const { ignored, status, onChange, onReady } = this.props
    if(status !== prevProps.status && status === 'success') onReady()
    if(!_.isEqual(ignored.sort(), prevProps.ignored.sort())) onChange(ignored)
  }

  _handleToggle(id) {
    this.props.onToggle(id)
  }

}

export default Subscriptions
