export const fetchAll = (channel_id, skip) => ({
  type: 'API_REQUEST',
  method: 'GET',
  endpoint: `/api/admin/chat/channels/${channel_id}/messages`,
  query: { $page: { skip } },
  request: 'FETCH_ALL_REQUEST',
  success: 'FETCH_ALL_SUCCESS',
  failure: 'FETCH_ALL_FAILURE'
})

export const fetch = (channel_id, id) => ({
  type: 'API_REQUEST',
  method: 'GET',
  endpoint: `/api/admin/chat/channels/${channel_id}/messages/${id}`,
  request: 'FETCH_REQUEST',
  success: 'FETCH_SUCCESS',
  failure: 'FETCH_FAILURE'
})

export const create = (channel_id, message) => ({
  type: 'API_REQUEST',
  method: 'POST',
  endpoint: `/api/admin/chat/channels/${channel_id}/messages`,
  body: { text: message.text, code: message.code, quoted_message_id: message.quoted_message_id },
  meta: { message },
  request: 'CREATE_REQUEST',
  success: 'CREATE_SUCCESS',
  failure: 'CREATE_FAILURE'
})

export const type = (text) => ({
  type: 'TYPE',
  text
})

export const add = (message) => ({
  type: 'ADD',
  message
})

export const showSignpost = (show) => ({
  type: 'SHOW_SIGNPOST',
  show
})

export const quoteMessage = (id) => ({
  type: 'QUOTE_MESSAGE',
  id
})

export const remove = (code) => ({
  type: 'REMOVE',
  code
})
