import React from 'react'

const Link = (attachment) => (
  <a href={ attachment.title_link } rel="noopener noreferrer" target="_blank" className="maha-attachment-link">
    { attachment.image_url &&
      <div className="maha-attachment-link-image">
        <img src={ attachment.image_url } />
      </div>
    }
    <div className="maha-attachment-details">
      <div className="maha-attachment-title">
        { attachment.title }
      </div>
      <div className="maha-attachment-text">
        { attachment.text }
      </div>
      <div className="maha-attachment-service">
        { attachment.service_icon && <img src={ attachment.service_icon } />}
        { attachment.service_name }
      </div>
    </div>
  </a>
)

export default Link
