import React from 'react'
import PropTypes from 'prop-types'
import { Assignment2, CompactUserToken } from 'maha-admin'

class Employees extends React.Component {

  static contextTypes = {
    modal: PropTypes.object
  }

  static propTypes = {
    employees: PropTypes.array,
    supervisor: PropTypes.object
  }

  render() {
    return <Assignment2 { ...this._getAssignment() } />
  }

  _getAssignment() {
    const { supervisor, employees } = this.props
    return {
      action: `/api/admin/team/supervisors/${supervisor.id}/employees`,
      defaultValue: employees.map(employee => employee.id),
      endpoint: `/api/admin/team/supervisors/${supervisor.id}/employees/all`,
      filters: [
        { label: 'Group', name: 'group_id', type: 'select', multiple: true, endpoint: '/api/admin/team/groups', value: 'id', text: 'title', sort: { key: 'title', order: 'asc' } }
      ],
      label: 'employee',
      multiple: true,
      format: CompactUserToken,
      text: 'full_name',
      title: 'Manage Employees',
      value: 'id'
    }
  }

}

export default Employees
