export const loadPush = () => ({
  type: 'LOCAL_GET',
  key: 'push',
  request: 'LOAD_PUSH_REQUEST',
  success: 'LOAD_PUSH_SUCCESS',
  failure: 'LOAD_PUSH_FAILURE'
})

export const savePush = (value) => ({
  type: 'LOCAL_SET',
  key: 'push',
  value,
  request: 'SAVE_PUSH_REQUEST',
  success: 'SAVE_PUSH_SUCCESS',
  failure: 'SAVE_PUSH_FAILURE'
})

export const saveSubscription = (body) => ({
  type: 'API_REQUEST',
  method: 'POST',
  endpoint: '/api/admin/push',
  body,
  request: 'SAVE_SUBSCRIPTION_REQUEST',
  success: 'SAVE_SUBSCRIPTION_SUCCESS',
  failure: 'SAVE_SUBSCRIPTION_FAILURE'
})
