import _ from 'lodash'

const INITIAL_STATE = {
  active: null,
  apps: null,
  fingerprint: null,
  status: 'pending',
  teams: null,
  user: null,
  presence: [],
  unseen: 0
}

export default (state = INITIAL_STATE, action) => {

  switch (action.type) {

  case 'LOAD_ADMIN_REQUEST':
    return {
      ...state,
      status: 'loading'
    }

  case 'LOAD_ADMIN_SUCCESS':
    return {
      ...state,
      fingerprint: _.random(100000000, 999999999).toString(36),
      ...action.value || { active: null, teams: [] },
      status: 'success'
    }

  case 'LOAD_ADMIN_FAILURE':
    return {
      ...state,
      status: 'failure'
    }

  case 'ADD_TEAM':
    return {
      ...state,
      active: state.teams ? state.teams.length : 0,
      teams: [
        ...state.teams,
        {
          ...action.team,
          token: action.token,
          user: action.user
        }
      ]
    }

  case 'SIGNIN':
    return {
      ...state,
      active: action.index,
      teams: [
        ...state.teams.slice(0, action.index),
        {
          ...state.teams[action.index],
          token: action.token
        },
        ...state.teams.slice(action.index + 1, state.teams.length)
      ]
    }

  case 'SIGNOUT_SUCCESS':
    return {
      ...state,
      active: null,
      teams: [
        ...state.teams.slice(0, state.active),
        _.omit(state.teams[state.active], ['token']),
        ...state.teams.slice(state.active + 1, state.teams.length)
      ]
    }

  case 'REMOVE_TEAM':
    return {
      ...state,
      teams: state.teams.filter((team, index) => (index !== action.index))
    }

  case 'REMOVE_ALL_TEAMS':
    return {
      ...state,
      teams: []
    }

  case 'SET_PRESENCE':
    return {
      ...state,
      presence: action.presence
    }

  case 'CHOOSE_TEAM':
    return {
      ...state,
      active: action.index
    }

  case 'LOAD_SESSION_SUCCESS':
    return {
      ...state,
      teams: [
        ...state.teams.slice(0, state.active),
        {
          ...state.teams[state.active],
          ...action.result.data.team,
          user: action.result.data.user
        },
        ...state.teams.slice(state.active + 1, state.teams.length)
      ],
      apps: action.result.data.apps,
      user: action.result.data.user
    }

  case 'REMOVE_SESSION':
    return {
      ...state,
      apps: null,
      user: null
    }

  case 'UPDATE_SESSION':
    return {
      ...state,
      ...action.session
    }

  case 'UPDATE_UNSEEN':
    return {
      ...state,
      unseen: state.unseen + action.difference
    }

  default:
    return state
  }

}
