import PropTypes from 'prop-types'
import React from 'react'

class Slide extends React.Component {

  static propTypes = {
    icon: PropTypes.string,
    image: PropTypes.string,
    video: PropTypes.string,
    title: PropTypes.string,
    text: PropTypes.string
  }

  render() {
    const { icon, title, text, image, video } = this.props
    return (
      <div className="maha-tour-slide">
        { (icon || video || image) &&
          <div className="maha-tour-slide-media">
            { icon && <i className={`fa fa-fw fa-${ icon }`} /> }
            { video &&
              <iframe width="560" height="315" src={ video } frameBorder="0" allow="autoplay; encrypted-media" allowFullScreen></iframe>
            }
            { image &&
              <img src={ image } />
            }
          </div>
        }
        <div className="maha-tour-slide-description">
          { title && <h2>{ title }</h2> }
          { text && <p>{ text }</p> }
        </div>
      </div>
    )

  }


}

export default Slide
