import { convertToRaw, CompositeDecorator, ContentState, Editor, EditorState, getDefaultKeyBinding, convertFromHTML, Modifier } from 'draft-js'
import { emoji, mention } from './decorators'
import Attachments from '../attachments'
import PropTypes from 'prop-types'
import Emojis from '../emojis'
import React from 'react'

class Composer extends React.Component {

  static contextTypes = {
    modal: PropTypes.object
  }

  static propTypes = {
    attachments: PropTypes.bool,
    defaultValue: PropTypes.string,
    emojis: PropTypes.bool,
    icon: PropTypes.string,
    placeholder: PropTypes.string,
    uploads: PropTypes.array,
    onChooseAssets: PropTypes.func,
    onChange: PropTypes.func,
    onFocus: PropTypes.func,
    onReady: PropTypes.func,
    onSetState: PropTypes.func,
    onSubmit: PropTypes.func,
    onToggleEmojis: PropTypes.func,
    onUpArrow: PropTypes.func
  }

  static defaultProps = {
    attachments: true,
    icon: 'comment',
    placeholder: 'Enter text',
    onChange: () => {},
    onReady: () => {},
    onSubmit: () => {},
    onUpArrow: () => {},
    onFocus: () => {}
  }

  state = {
    editorState: null
  }

  _handleAttachments = this._handleAttachments.bind(this)
  _handleChange = this._handleChange.bind(this)
  _handleChooseAssets = this._handleChooseAssets.bind(this)
  _handleInput = this._handleInput.bind(this)
  _handleInsertEmoji = this._handleInsertEmoji.bind(this)
  _handleKeyBinding = this._handleKeyBinding.bind(this)
  _handleKeyCommand = this._handleKeyCommand.bind(this)
  _handleFocus = this._handleFocus.bind(this)
  _handleSave = this._handleSave.bind(this)
  _handleToggleEmojis = this._handleToggleEmojis.bind(this)
  _handleUpArrow = this._handleUpArrow.bind(this)

  render() {
    const { editorState } = this.state
    const { attachments, emojis } = this.props
    return (
      <div className="maha-composer">
        { emojis && <Emojis { ...this._getEmojis() } /> }
        <div className="maha-composer-component">
          { attachments &&
            <div className="maha-composer-attachment" onClick={ this._handleAttachments }>
              <i className="fa fa-fw fa-plus" />
            </div>
          }
          <div className="maha-composer-input">
            <div className="maha-composer-text">
              <div className="maha-composer-editor" ref={ node => this.composer = node }>
                { editorState && <Editor { ...this._getEditor() } /> }
              </div>
              <div className="maha-composer-emojis" onClick={ this._handleToggleEmojis }>
                <i className="fa fa-fw fa-smile-o" />
              </div>
            </div>
          </div>
          <div className="maha-composer-send" onClick={ this._handleSave }>
            <i className="fa fa-fw fa-paper-plane" />
          </div>
        </div>
      </div>
    )
  }

  componentDidMount() {
    const { defaultValue } = this.props
    const editorState = this._getInitialState(defaultValue)
    this._handleChange(editorState)
    this.props.onReady()
  }

  _getInitialState(value) {
    const decorator = new CompositeDecorator([ mention, emoji ])
    if(!value) return EditorState.createEmpty(decorator)
    const blocksFromHTML = convertFromHTML(value.replace(/\n/g, '<br />'))
    const contentState = ContentState.createFromBlockArray(blocksFromHTML)
    const currentState = EditorState.createWithContent(contentState, decorator)
    const selectionState = EditorState.moveSelectionToEnd(currentState).getSelection()
    return EditorState.forceSelection(currentState, selectionState)
  }

  _getEditor() {
    const { editorState } = this.state
    const { placeholder } = this.props
    return {
      ref: node => this.el = node,
      editorState,
      onChange: this._handleChange,
      placeholder,
      keyBindingFn: this._handleKeyBinding,
      handleBeforeInput: this._handleInput,
      handleKeyCommand: this._handleKeyCommand,
      onFocus: this._handleFocus,
      onUpArrow: this._handleUpArrow
    }
  }

  _getAttachments() {
    return {
      prompt: 'Upload File(s)',
      onChooseAssets: this._handleChooseAssets
    }
  }

  _getEmojis() {
    return {
      onChange: this._handleInsertEmoji,
      onClose: this._handleToggleEmojis
    }
  }

  _handleChooseAssets(assets) {
    const { onChooseAssets } = this.props
    onChooseAssets(assets)
  }

  _handleAttachments() {
    this.context.modal.open(<Attachments { ...this._getAttachments() } />)
  }

  _handleToggleEmojis() {
    this.props.onToggleEmojis()
  }

  _handleInsertEmoji(emoji) {
    const { editorState } = this.state
    const selection = editorState.getSelection()
    const contentState = editorState.getCurrentContent()
    const ncs = Modifier.insertText(contentState, selection, emoji)
    const newState = EditorState.push(editorState, ncs, 'insert-fragment')
    this._handleChange(newState)
  }

  _handleReset() {
    const { editorState } = this.state
    const resetEditorState = EditorState.push(editorState, ContentState.createFromText(''))
    this._handleChange(EditorState.moveFocusToEnd(resetEditorState))
  }

  _handleChange(editorState) {
    const { onChange } = this.props
    this.setState({ editorState })
    const converted = this._getConverted(editorState)
    onChange(converted)
  }

  _handleInput(string, editorState) {
    return false
  }

  _handleKeyBinding(e) {
    if(e.keyCode === 13 && !e.nativeEvent.shiftKey) {
      return 'message-save'
    }
    return getDefaultKeyBinding(e)
  }

  _handleKeyCommand(command) {
    if (command === 'message-save') {
      this._handleSave()
      return 'handled'
    }
    return 'not-handled'
  }

  _handleUpArrow(e) {
    e.preventDefault()
    this.props.onUpArrow()
  }

  _handleFocus() {
    this.props.onFocus()
  }

  _getConverted(editorState) {
    const state = convertToRaw(editorState.getCurrentContent())
    return state.blocks.reduce((content, block) => [
      ...content,
      block.text
    ], []).join('\r\n')

  }

  _handleSave() {
    const { editorState } = this.state
    const { onSubmit } = this.props
    const converted = this._getConverted(editorState)
    if(converted.length === 0) return
    onSubmit(converted)
    this._handleReset()
  }

}

export default Composer
