import _ from 'lodash'

const INITIAL_STATE = {
  online: null,
  channels: {},
  handlers: [],
  presence: null
}

export default (state = INITIAL_STATE, action) => {

  switch (action.type) {

  case 'CONNECT':
    return {
      ...state,
      online: true
    }

  case 'DISCONNECT':
    return {
      ...state,
      online: false,
      channels: Object.keys(state.channels).reduce((channels, channel) => ({
        ...channels,
        [channel]: false
      }), {})
    }

  case 'JOIN_SUCCESS':
    return {
      ...state,
      channels: {
        ...state.channels,
        [action.channel]: true
      }
    }

  case 'LEAVE_SUCCESS':
    return {
      ...state,
      channels: {
        ..._.omit(state.channels, action.channel)
      }
    }

  case 'SUBSCRIBE':
    return {
      ...state,
      handlers: [
        ...state.handlers,
        action.handler
      ]
    }

  case 'UNSUBSCRIBE':
    return {
      ...state,
      handlers: [
        ...state.handlers.filter(handler => !_.isEqual(handler, action.handler))
      ]
    }

  case 'PRESENCE':
    return {
      ...state,
      presence: action.handler
    }


  default:
    return state
  }

}
