import { createSelector } from 'reselect'
import moment from 'moment'
import _ from 'lodash'

const quoted_message_id = (state, props) => state.quoted_message_id

const unsorted = (state, props) => state.messages

export const messages = createSelector(
  unsorted,
  (unsorted) => {
    return unsorted.slice().reverse().reduce((messages, message) => {
      const delta = moment(message.created_at).diff(messages.created_at, 'minutes')
      const newday = moment(message.created_at).format('D') !== moment(messages.created_at).format('D')
      const full = message.user.id !== messages.user_id || delta > 5
      return {
        user_id: message.user.id,
        created_at: message.created_at,
        messages: [
          ...messages.messages,
          {
            ...message,
            newday,
            full
          }
        ]
      }
    }, { messages: [], user_id: null, created_at: null }).messages
  })

export const quoted_message = createSelector(
  unsorted,
  quoted_message_id,
  (messages, id) => _.find(messages, { id })
)
