export const INITIAL_STATE = {
  messages: [],
  status: 'pending',
  text: '',
  total: 0,
  signpost: false,
  quoted_message_id: null
}

const reducer = (state = INITIAL_STATE, action) => {

  switch (action.type) {

  case 'FETCH_ALL_REQUEST':
    return {
      ...state,
      status: state.status ? 'loading' : 'appending'
    }

  case 'FETCH_ALL_FAILURE':
    return {
      ...state,
      status: 'failure'
    }

  case 'FETCH_ALL_SUCCESS':
    return {
      ...state,
      total: action.result.pagination.total,
      messages: [
        ...state.messages,
        ...action.result.data
      ],
      status: 'success'
    }

  case 'CREATE_REQUEST':
    return {
      ...state,
      messages: [
        action.message,
        ...state.messages
      ],
      quoted_message_id: null,
      status: 'success',
      text: ''
    }

  case 'CREATE_FAILURE':
    return {
      ...state,
      messages: [
        ...state.messages.filter(message => message.code !== action.message.code)
      ]
    }

  case 'CREATE_SUCCESS':
    return {
      ...state,
      messages: [
        ...state.messages.map(message => {
          return (message.code === action.result.data.code) ? action.result.data : message
        })
      ],
      quoted_message_id: null,
      text: ''
    }

  case 'TYPE':
    return {
      ...state,
      text: action.text
    }

  case 'ADD':
    return {
      ...state,
      messages: [
        action.message,
        ...state.messages.filter(message => message.code !== action.message.code)
      ]
    }

  case 'SHOW_SIGNPOST':
    return {
      ...state,
      signpost: action.show
    }

  case 'QUOTE_MESSAGE':
    return {
      ...state,
      quoted_message_id: action.id
    }

  case 'REMOVE':
    return {
      ...state,
      messages: [
        ...state.messages.filter(message => message.code !== action.code)
      ]
    }
    
  default:
    return state

  }

}

export default reducer
