import PropTypes from 'prop-types'
import { Page } from 'maha-admin'
import moment from 'moment'
import React from 'react'

const EnvelopeToken = ({ sent_at, subject, to, was_opened }) => (
  <div className="email-token">
    <div className="email-token-icon">
      { was_opened && <i className="fa fa-fw fa-envelope-open" /> }
      { !was_opened && <i className="fa fa-fw fa-envelope" /> }
    </div>
    <div className="email-token-details">
      <small>{ _relativeTime(sent_at) }</small><br />
      <strong>{ to.match(/([^<]*).*/)[1] }</strong><br />
      {subject }
    </div>
  </div>
)

EnvelopeToken.propTypes = {
  sent_at: PropTypes.object,
  subject: PropTypes.string,
  to: PropTypes.string,
  was_opened: PropTypes.object
}

const _relativeTime = (sent_at) => {
  if(!sent_at) return 'NOT SENT'
  const datetime = moment(sent_at)
  if(datetime.isSame(moment(), 'd')) return datetime.format('h:mm A')
  if(datetime.isSame(moment().subtract(1, 'day'), 'd')) return 'Yesterday'
  return datetime.format('MMM D, YYYY')
}

const mapPropsToPage = (props, context, resources, page) => ({
  title: 'Sent Mail',
  rights: ['team:manage_team'],
  collection: {
    endpoint: '/api/admin/team/emails',
    table: [
      { label: 'ID', key: 'id', visible: false, collapsing: true },
      { label: 'To', key: 'to', visible: false },
      { label: 'Subject', key: 'subject', primary: true, format: EnvelopeToken },
      { label: 'Status', key: 'status', visible: false },
      { label: 'Sent At', key: 'sent_at', visible: false }
    ],
    export: [
      { label: 'ID', key: 'id' },
      { label: 'To', key: 'to' },
      { label: 'Subject', key: 'subject' },
      { label: 'Status', key: 'status' },
      { label: 'Sent At', key: 'sent_at' }
    ],
    icon: 'envelope',
    entity: 'email',
    filters: [
      { label: 'User', name: 'user_id', type: 'select', multiple: true, endpoint: '/api/admin/team/users', value: 'id', text: 'full_name', sort: { key: 'last_name', order: 'asc' } },
      { label: 'Sent', name: 'sent_at', type: 'daterange', include: ['this','last'] }
    ],
    link: (record) => `/admin/team/emails/${record.id}`,
    defaultSort: { key: 'created_at', order: 'desc' }
  }
})

export default Page(null, mapPropsToPage)
