import { Star, AssetThumbnail } from 'maha-admin'
import { connect } from 'react-redux'
import PropTypes from 'prop-types'
import React from 'react'

class Items extends React.Component {

  static contextTypes = {
    admin: PropTypes.object,
    router: PropTypes.object,
    tasks: PropTypes.object
  }

  static propTypes = {
    folder: PropTypes.object,
    preview: PropTypes.object,
    records: PropTypes.array,
    onChangeFolder: PropTypes.func,
    onDrive: PropTypes.func,
    onPreview: PropTypes.func,
    onShared: PropTypes.func,
    onStarred: PropTypes.func,
    onTasks: PropTypes.func,
    onTrash: PropTypes.func,
    onUpdateFile: PropTypes.func
  }

  _handlePreview = this._handlePreview.bind(this)
  _handleDrive = this._handleDrive.bind(this)
  _handleShared = this._handleShared.bind(this)
  _handleStarred = this._handleStarred.bind(this)
  _handleTasks = this._handleTasks.bind(this)
  _handleTrash = this._handleTrash.bind(this)
  _handleUpdate = this._handleUpdate.bind(this)

  render() {
    const { folder, records } = this.props
    return (
      <div className="drive-items">
        { folder.code !== 'root' && records.map((item, index) => (
          <div { ...this._getItem(item) } key={`item_${item.code}`}>
            <div className="drive-item-icon">
              { item.type === 'folder' &&
                <div className="maha-asset-icon">
                  <i className="fa fa-fw fa-folder" />
                </div>
              }
              { item.type === 'file' && <AssetThumbnail { ...item.asset } /> }
            </div>
            <div className="drive-item-name">
              { item.label }
            </div>
            { folder.code !== 'trash' &&
              <div className="drive-item-action">
                <Star { ...this._getStar(item) } />
              </div>
            }
            <div className="drive-item-action" onClick={ this._handleTasks.bind(this, item) }>
              <i className="fa fa-fw fa-ellipsis-v" />
            </div>
          </div>
        ))}
        { folder.code === 'root' &&
          <div className="drive-item" onClick={ this._handleDrive }>
            <div className="drive-item-icon">
              <div className="maha-asset-icon">
                <i className="fa fa-fw fa-hdd-o" />
              </div>
            </div>
            <div className="drive-item-name">
              My Drive
            </div>
            <div className="drive-item-action">
              <i className="fa fa-fw fa-chevron-right" />
            </div>
          </div>
        }
        { folder.code === 'root' &&
          <div className="drive-item" onClick={ this._handleShared }>
            <div className="drive-item-icon">
              <div className="maha-asset-icon">
                <i className="fa fa-fw fa-handshake-o" />
              </div>
            </div>
            <div className="drive-item-name">
              Shared With Me
            </div>
            <div className="drive-item-action">
              <i className="fa fa-fw fa-chevron-right" />
            </div>
          </div>
        }
        { folder.code === 'root' &&
          <div className="drive-item" onClick={ this._handleStarred }>
            <div className="drive-item-icon">
              <div className="maha-asset-icon">
                <i className="fa fa-fw fa-star" />
              </div>
            </div>
            <div className="drive-item-name">
              Starred Items
            </div>
            <div className="drive-item-action">
              <i className="fa fa-fw fa-chevron-right" />
            </div>
          </div>
        }
        { folder.code === 'root' &&
          <div className="drive-item" onClick={ this._handleTrash }>
            <div className="drive-item-icon">
              <div className="maha-asset-icon">
                <i className="fa fa-fw fa-trash" />
              </div>
            </div>
            <div className="drive-item-name">
              Trash
            </div>
            <div className="drive-item-action">
              <i className="fa fa-fw fa-chevron-right" />
            </div>
          </div>
        }
      </div>
    )
  }

  _getItem(item) {
    return {
      className: this._getClass(item),
      onContextMenu: this._handleTasks.bind(this, item),
      onClick: this._handleClick.bind(this, item),
      onDoubleClick: this._handleView.bind(this, item)
    }
  }

  _getClass(item) {
    const { preview } = this.props
    const classes= ['drive-item']
    if(preview && preview.code === item.code) classes.push('selected')
    return classes.join(' ')
  }

  _getStar(item) {
    return {
      table: item.type === 'folder' ? 'drive_folders' : 'drive_files',
      id: item.item_id,
      is_starred: item.is_starred
    }
  }

  _handleClick(item, e) {
    e.stopPropagation()
    this._handlePreview(item)
    if(item.type === 'folder') this._handleChangeFolder(item)
    if(document.body.clientWidth <= 768) this._handleView(item)
  }

  _handleView(item) {
    const { router } = this.context
    if(item.type === 'file') router.push(`/admin/drive/files/${item.code}`)
  }

  _handleDrive(e) {
    e.stopPropagation()
    this.props.onDrive()
  }

  _handleShared(e) {
    e.stopPropagation()
    this.props.onShared()
  }

  _handleStarred(e) {
    e.stopPropagation()
    this.props.onStarred()
  }

  _handleTasks(item, e) {
    this.props.onTasks(item, e)
  }

  _handleTrash(e) {
    e.stopPropagation()
    this.props.onTrash()
  }

  _handleChangeFolder(item) {
    this.props.onChangeFolder(item)
  }

  _handleDownload(item) {
    const { token } = this.context.admin.team
    window.location.href = `/api/admin/assets/${item.asset.id}/download?token=${token}`
  }

  _handlePreview(item) {
    this.props.onPreview(item)
  }

  _handleUpdate(item, asset) {
    this.props.onUpdateFile(item.code, asset.id)
  }

}

const mapStateToProps = (state, props) => ({
  preview: state.drive.explorer.preview
})

export default connect(mapStateToProps)(Items)
