const INITIAL_STATE = {
  reviews: [],
  editing: null,
  q: '',
  q_saved: '',
  status: 'pending',
  score: 0,
  score_saved: 0,
  typing: null,
  hidden: 0
}

const setTyping = (state, action) => ({
  ...state,
  typing: action.typing
})

const showMore = (state, action) => ({
  ...state,
  hidden: 0
})

const type = (state, action) => ({
  ...state,
  q: action.q
})

const rate = (state, action) => ({
  ...state,
  score: action.score
})

const beginEdit = (state, action) => ({
  ...state,
  editing: action.uid,
  q: action.q
})

const cancelEdit = (state, action) => ({
  ...state,
  editing: null,
  q: ''
})

const add = (state, action) => ({
  ...state,
  reviews: [
    ...state.reviews.filter(review => review.uid !== action.review.uid),
    action.review
  ]
})

const replace = (state, action) => ({
  ...state,
  reviews: [
    ...state.reviews.map(review => (review.uid !== action.review.uid) ? review : action.review)
  ]
})

const remove = (state, action) => ({
  ...state,
  reviews: [
    ...state.reviews.filter(review => review.uid !== action.uid)
  ]
})

const destroyRequest = (state, action) => ({
  ...state,
  reviews: [
    ...state.reviews.filter(review => review.uid !== action.review.uid)
  ]
})

const loadRequest = (state, action) => ({
  ...state,
  status: 'loading'
})

const loadSuccess = (state, action) => ({
  ...state,
  status: 'loaded',
  reviews: action.result.data,
  hidden: action.result.data.length - 4
})

const loadFailure = (state, action) => ({
  ...state,
  status: 'failed'
})

const createRequest = (state, action) => ({
  ...state,
  q: '',
  q_saved: state.q,
  score: 0,
  score_saved: state.score,
  reviews: [
    ...state.reviews,
    action.review
  ]
})

const createFailure = (state, action) => ({
  ...state,
  q: state.q_saved,
  q_saved: '',
  score: state.score_saved,
  score_saved: 0,
  reviews: [
    ...state.reviews.filter(review => review.uid !== action.review.uid)
  ]
})

const createSuccess = (state, action) => ({
  ...state,
  q: '',
  q_saved: '',
  score: 0,
  score_saved: 0
})

const updateRequest = (state, action) => ({
  ...state,
  editing: null,
  q: '',
  q_saved: state.q,
  score: 0,
  score_saved: state.score,
  reviews: [
    ...state.reviews.map(review => {
      if(review.uid !== action.review.uid) return review
      return {
        ...review,
        text: action.review.text,
        updated_at: action.review.updated_at
      }
    })
  ]
})

const updateFailure = (state, action) => ({
  ...state,
  editing: action.review.uid,
  q: state.q_saved,
  q_saved: '',
  score: state.score_saved,
  score_saved: 0,
  reviews: [
    ...state.reviews.map(review => {
      if(review.uid !== action.review.uid) return review
      return {
        ...review,
        text: state.text
      }
    })
  ]
})

const updateSuccess = (state, action) => ({
  ...state,
  q: '',
  q_saved: '',
  score: 0,
  score_saved: 0
})

const reducer = (state = INITIAL_STATE, action) => {

  switch (action.type) {

  case 'SET_TYPING':
    return setTyping(state, action)

  case 'SHOW_MORE':
    return showMore(state, action)

  case 'TYPE':
    return type(state, action)

  case 'RATE':
    return rate(state, action)

  case 'BEGIN_EDIT':
    return beginEdit(state, action)

  case 'CANCEL_EDIT':
    return cancelEdit(state, action)

  case 'CREATE_REQUEST':
    return createRequest(state, action)

  case 'CREATE_FAILURE':
    return createFailure(state, action)

  case 'CREATE_SUCCESS':
    return createSuccess(state, action)

  case 'LOAD_REQUEST':
    return loadRequest(state, action)

  case 'LOAD_FAILURE':
    return loadFailure(state, action)

  case 'LOAD_SUCCESS':
    return loadSuccess(state, action)

  case 'UPDATE_REQUEST':
    return updateRequest(state, action)

  case 'UPDATE_FAILURE':
    return updateFailure(state, action)

  case 'UPDATE_SUCCESS':
    return updateSuccess(state, action)

  case 'DESTROY_REQUEST':
    return destroyRequest(state, action)

  case 'ADD':
    return add(state, action)

  case 'REPLACE':
    return replace(state, action)

  case 'REMOVE':
    return remove(state, action)

  default:
    return state
  }

}

export default reducer
