import _ from 'lodash'

export const INITIAL_STATE = {
  assigned: [],
  member_types: [],
  adding: false,
  status: 'pending'
}

const reducer = (state = INITIAL_STATE, action) => {

  switch (action.type) {

  case 'FETCH_ASSIGNED_SUCCESS':
    return {
      ...state,
      assigned: action.result.data
    }

  case 'FETCH_MEMBER_TYPES_SUCCESS':
    return {
      ...state,
      member_types: action.result.data
    }

  case 'SAVE_SUCCESS':
    return {
      ...state,
      status: 'saved'
    }

  case 'ADD':
    return {
      ...state,
      adding: false,
      assigned: [
        ...state.assigned,
        { [action.model]: action.item, member_type: state.member_types[2] }
      ]
    }

  case 'REMOVE':
    return {
      ...state,
      assigned: [
        ...state.assigned.slice(0, action.index),
        ...state.assigned.slice(action.index + 1)
      ]
    }

  case 'CHANGE_TYPE':
    const assigned = state.assigned[action.index]
    const member_type_index = _.findIndex(state.member_types, { id: assigned.member_type.id })
    const next_member_type_index = (member_type_index + 1) % 3
    return {
      ...state,
      assigned: [
        ...state.assigned.slice(0, action.index),
        {
          ...state.assigned[action.index],
          member_type: state.member_types[next_member_type_index]
        },
        ...state.assigned.slice(action.index + 1)
      ]
    }

  case 'BEGIN_ADD':
    return {
      ...state,
      adding: true
    }

  default:
    return state

  }

}

export default reducer
