import { connect } from 'react-redux'
import React from 'react'
import PropTypes from 'prop-types'
import Avatar from '../avatar'
import Edit from './edit'
import Photo from './photo'
import Password from './password'
import Preferences from './preferences'
import Profiles from '../profiles'

class Account extends React.Component {

  static contextTypes = {
    admin: PropTypes.object,
    modal: PropTypes.object,
    session: PropTypes.object
  }

  static propTypes = {
    user: PropTypes.object,
    onDone: PropTypes.func
  }

  static defaultProps = {
    onDone: () => {}
  }

  _handleEdit = this._handleModal.bind(this, Edit)
  _handlePhoto = this._handleModal.bind(this, Photo)
  _handlePassword = this._handleModal.bind(this, Password)
  _handleProfiles = this._handleModal.bind(this, Profiles)
  _handlePreferences = this._handleModal.bind(this, Preferences)
  _handleSignout = this._handleSignout.bind(this)

  render() {
    const { user } = this.props
    return (
      <div className="maha-account-panel">
        <div className="maha-account-identity">
          <Avatar user={ user } width="150" presence={ false } />
          <h2>{ user.full_name }</h2>
          <p>{ user.email }</p>
        </div>
        <div className="maha-account-tasks">
          <div className="maha-account-task" onClick={ this._handleEdit }>
            <i className="fa fa-fw fa-pencil" /> Edit account
          </div>
          <div className="maha-account-task" onClick={ this._handlePhoto }>
            <i className="fa fa-fw fa-picture-o" /> { user.photo ? 'Change photo' : 'Upload photo' }
          </div>
          <div className="maha-account-task" onClick={ this._handlePassword }>
            <i className="fa fa-fw fa-lock" /> Change password
          </div>
          <div className="maha-account-task" onClick={ this._handleProfiles }>
            <i className="fa fa-fw fa-id-card" /> Manage profiles
          </div>
          <div className="maha-account-task" onClick={ this._handlePreferences }>
            <i className="fa fa-fw fa-bell" /> Configure notifications
          </div>
          <div className="maha-account-task" onClick={ this._handleSignout }>
            <i className="fa fa-fw fa-power-off" /> Sign out
          </div>
        </div>
      </div>
    )
  }

  _handleModal(component) {
    this.props.onDone()
    setTimeout(() => this.context.modal.open(component), 250)
  }

  _handleCloseDrawer() {
    this.props.onDone()
  }

  _handleSignout() {
    this.props.onDone()
    this.context.admin.signout()
  }

}

const mapStateToProps = (state, props) => ({
  user: state.maha.admin.user
})

export default connect(mapStateToProps)(Account)
