import emojiUnicode from 'emoji-unicode'
import React from 'react'

const EMOJI_REGEX = /(?:[\u2700-\u27bf]|(?:\ud83c[\udde6-\uddff]){2}|[\ud800-\udbff][\udc00-\udfff])[\ufe0e\ufe0f]?(?:[\u0300-\u036f\ufe20-\ufe23\u20d0-\u20f0]|\ud83c[\udffb-\udfff])?(?:\u200d(?:[^\ud800-\udfff]|(?:\ud83c[\udde6-\uddff]){2}|[\ud800-\udbff][\udc00-\udfff])[\ufe0e\ufe0f]?(?:[\u0300-\u036f\ufe20-\ufe23\u20d0-\u20f0]|\ud83c[\udffb-\udfff])?)*/g

const findWithRegex = (regex) => (contentBlock, callback, contentState) => {
  const text = contentBlock.getText()
  let matchArr, start
  while ((matchArr = regex.exec(text)) !== null) {
    start = matchArr.index
    callback(start, start + matchArr[0].length)
  }
}

export const mention = {
  strategy: findWithRegex(/@[\w]+/g),
  component: ({ children, offsetKey }) => (
    <span className="maha-composer-mention" data-offset-key={ offsetKey }>
      { children }
    </span>
  )
}
export const emoji = {
  strategy: findWithRegex(EMOJI_REGEX),
  component: ({ children, decoratedText, offsetKey }) => {

    const destructured = [...decoratedText]

    const token = destructured ? destructured.map(code => emojiUnicode(code)).join('-') : decoratedText

    return (
      <span className={`emoji emoji-${token}`} data-offset-key={ offsetKey }>
        { children }
      </span>
    )

  }
}
